<template>
  <el-table
    :data="projectlist"
    v-loading="loading"
    border
    stripe
    center
    size="small"
    max-height="400"
    style="width: 100%">
    <el-table-column
      fixed
      prop="projectnamePro"
      label="工程名称"
       align="center"
      min-width="150">
    </el-table-column>
    <el-table-column
      prop="prousernamePro"
      label="创建用户"
       align="center"
      min-width="120">
    </el-table-column>
    <el-table-column
      prop="createdatePro"
      label="创建时间"
       align="center"
      min-width="180">
    </el-table-column>
    <el-table-column
     show-overflow-tooltip
      prop="remarkPro"
       align="center"
      label="备注"
      min-width="120">
    </el-table-column>
    <el-table-column
      fixed="right"
      align="center"
      label="操作"
      min-width="160">
      <template slot-scope="scope">
        <el-button @click="handleDeleteClick(scope.row)" type="text" size="small">删除</el-button>
        <el-button @click="handleOpenClick(scope.row)" type="text" size="small">备份信息</el-button>
        <!-- <el-button type="text" size="small">编辑</el-button> -->
      </template>
    </el-table-column>
  </el-table>
</template>
<script>
export default {
methods: {
      handleDeleteClick(row) {
         this.$emit('deleteproject',row.projectidPro);
      },
      handleOpenClick(row){
         this.$emit('openprojectaff',row);
      }
    },
    props:{
      projectlist:{
        type:Array
      },
      loading:{
        type:Boolean,
        default:false
      }
    },
    data() {
      return {
        
      }
  }
}
</script>