<template>
  <div>
    <el-steps :active="selectactive" finish-status="success" simple style="margin-top: 20px">
        <el-step title="基础设置" ></el-step>
        <el-step title="流体相关" ></el-step>
        <el-step title="加热炉" ></el-step>
        <el-step title="侵蚀/腐蚀" ></el-step>
        <el-step title="环境设置" ></el-step>
        <el-step title="高级设置" ></el-step>
    </el-steps>
    <simulate-basic style="padding-top:20px;padding-left:25px;padding-right:25px" v-show="selectactive===0" :newsimulationmodel="newsimulationmodel"></simulate-basic>
    <flow-correlation  style="padding-top:20px;padding-left:25px;padding-right:25px" v-show="selectactive===1"></flow-correlation>
    <heat-transfe  style="padding-top:20px;padding-left:25px;padding-right:25px" v-show="selectactive===2"></heat-transfe>
    <erosion-corrosion  style="padding-top:20px;padding-left:25px;padding-right:25px" v-show="selectactive===3"></erosion-corrosion>
    <environ-mental  style="padding-top:20px;padding-left:25px;padding-right:25px" v-show="selectactive===4"></environ-mental>
    <advan-ced  style="padding-top:20px;padding-left:25px;padding-right:25px" v-show="selectactive===5"></advan-ced>
    <el-button style="margin-top: 12px;" @click="goup">上一步</el-button>
    <el-button style="margin-top: 12px;" @click="next">下一步</el-button>

  </div>
</template>
<script>
import FlowCorrelation from "./simulatesets/flowcorrelation";
import HeatTransfe from "./simulatesets/heattransfe";
import EnvironMental from "./simulatesets/environmental";
import AdvanCed from "./simulatesets/advanced";
import SimulateBasic from "./simulatesets/simulatebasic";
import ErosionCorrosion  from "./simulatesets/erosioncorrosion"
export default {
    data(){
      return{
        //基础设置 Model
        newsimulationmodel:{
          simulationnameSim:"",
          remarkSim:""
        },
        selectactive:0,
      }
    },
    components:{
      FlowCorrelation,
      HeatTransfe,
      EnvironMental,
      AdvanCed,
      SimulateBasic,
      ErosionCorrosion
    },
    methods:{
      next(){
       
        if (this.selectactive++ > 4) this.selectactive = 0;
      },
      goup(){
         if (this.selectactive-- < 0) this.selectactive = 5;
      }
    }
}
</script>