顶部菜单
<template>
 <div> 
  <el-menu
    :default-active="activeIndex"
    class="el-menu-demo"
    mode="horizontal"
    @select="handleSelect"
    background-color="#545c64"
    text-color="#fff"
    active-text-color="#ffd04b"
  >
    <el-submenu index="1">
      <template slot="title">管网物理模型管理</template>
      <el-menu-item index="newproject">新建工程</el-menu-item>
      <el-menu-item index="openproject">打开工程</el-menu-item>
      <el-menu-item index="saveproject">保存工程</el-menu-item>
      <el-menu-item index="projectmanage">工程管理</el-menu-item>
    </el-submenu>
      <el-submenu index="2">
      <template slot="title">工具</template>
      <el-menu-item index="topoanalyze">模型拓扑分析</el-menu-item>
      <el-menu-item index="moxingjiancha">模型检查</el-menu-item>
      <el-menu-item index="1-3">批量参数设置</el-menu-item>
    </el-submenu>
    <el-submenu index="3">
      <template slot="title">管网仿真模拟</template>
      <el-menu-item index="simulatemanage">仿真模拟方案管理</el-menu-item>
      <el-menu-item index="simulateset">仿真模拟及参数预置</el-menu-item>
    </el-submenu>
    <el-submenu index="4">
      <template slot="title">管网运行分析</template>
      <el-menu-item index="1-1">管网运行指标分析</el-menu-item>
      <el-menu-item index="1-2">管网指标监测预警</el-menu-item>
      <el-menu-item index="1-2">水合物风险评估</el-menu-item>
      <el-menu-item index="1-2">积液预测</el-menu-item>
      <el-menu-item index="1-2">管网运行效率分析</el-menu-item>
      <el-menu-item index="1-2">决策辅助信息推送</el-menu-item>
    </el-submenu>
  <el-submenu index="5">
      <template slot="title">管网运行方案优选/优化</template>
      <el-menu-item index="1-1">运行优选方案管理</el-menu-item>      
      <el-menu-item index="1-2">方案分析优选</el-menu-item>
      <el-menu-item index="1-2">运行优化方案管理</el-menu-item>
      <el-menu-item index="1-2">运行方案优化</el-menu-item>
    </el-submenu>
  </el-menu>
    <!-- 新建工程 -->
    <el-dialog
    title="新建工程"
    :visible.sync="newprojectopendialog"
    width="30%"
   >
   <new-project></new-project>
    </el-dialog>
    <!-- 打开工程 -->
    <el-dialog
      title="打开工程"
      :visible.sync="openprojectopendialog"
      width="50%"
      >
      <open-project :projectlist="projectlist" @openprojectfun="openprojectfun" :loading="openprojectloading"></open-project>
    </el-dialog>
    <!-- 工程管理 -->
    <el-dialog
      title="工程管理"
      :visible.sync="projectmanageropendialog"
      width="50%"
      >
      <project-manager :projectlist="projectlist" @deleteproject="deleteproject" @openprojectaff="openprojectaff" :loading="openprojectloading"></project-manager>
    </el-dialog>
     <!-- 备份管理 -->
    <el-dialog
      title="工程备份管理"
      :visible.sync="projectaffmanageropendialog"
      width="50%"
      >
      <projectaff-manager :projectafflist="projectafflist" @deleteprojectaff="deleteprojectaff" @projectaffidbyopenproject="projectaffidbyopenproject" :loading="openprojectaffloading"></projectaff-manager>
    </el-dialog>
     <!-- 备份管理 -->
    <el-dialog
      title="仿真模拟方案管理"
      :visible.sync="simulatemanageopendialog"
      width="50%"
      >
      <simulate-manage :simulatemanagelist="simulatemanagelist" @getsimulatemanagelist="getsimulatemanagelist" :loading="opensimulatemanageloading"></simulate-manage>
    </el-dialog>
     <!-- 备份管理 -->
    <el-dialog
      title="仿真模拟及参数预制"
      :visible.sync="simulatesetopendialog"
      width="60%"
      >
      <simulate-set ></simulate-set>
    </el-dialog>
  </div> 
</template>
<script>
import NewProject from "../munedialog/wulimoxingguanli/newproject";
import OpenProject from "../munedialog/wulimoxingguanli/openproject";
import ProjectManager from "../munedialog/wulimoxingguanli/projectmanager";
import ProjectaffManager from "../munedialog/wulimoxingguanli/projectaffmanager";
import SimulateManage from "../munedialog/fangzhenmoni/simulatemanage";
import SimulateSet from "../munedialog/fangzhenmoni/simulateset";
import {
  getProjectListApi,
  proaffcountbyprojectidApi,
  getproaffbyprojectidlistApi
} from '@/api/gwwlmxgl'
import {
  getsimulationlistApi
} from '@/api/fzmnfa'
export default {
  data() {
    return {
      activeIndex: "1",
      activeIndex2: "1",
      //新建工程弹出框隐藏/显示 
      newprojectopendialog:false,
      //新建工程的Model
      newprojectmodel:[],
      //打开工程弹出框隐藏/显示
      openprojectopendialog:false,
      //工程管理弹出框隐藏/显示
      projectmanageropendialog:false,
      //工程备份管理弹出框隐藏/显示
      projectaffmanageropendialog:false,
      //仿真模拟方案管理弹出框隐藏/显示
      simulatemanageopendialog:false,
      //仿真模拟设置
      simulatesetopendialog:false,
      //工程列表
      projectlist:[],
      openprojectloading:false,
      //工程备份表管理
      projectafflist:[],
      openprojectaffloading:false,
      //仿真模拟方案管理
      simulatemanagelist:[],
      opensimulatemanageloading:false
    };
  },
  props:{
    project:{
      default:Array
    }
  },
  components:{
    NewProject,
    OpenProject,
    ProjectManager,
    ProjectaffManager,
    SimulateManage,
    SimulateSet
  },
  methods: {
    handleSelect(key) {
      switch (key) {
        case "newproject":
          this.newprojectopendialog=true;
          break;
        case "openproject":
          this.openprojectopendialog=true
          this.openprojectloading=true;
          this.getprojectlist();
          break;
        case "saveproject":
          this.saveprojectfun();
        break;
        case "projectmanage":
          this.projectmanageropendialog=true
          this.openprojectloading=true;
          this.getprojectlist();
        break;
        case "simulatemanage":
          this.simulatemanageopendialog=true;
          this.getsimulatemanagelist(2)
          break;
        case "simulateset":
          this.simulatesetopendialog=true;
          break;
        default:
          break;
      }
    },
    //获取仿真模拟方案列表
    getsimulatemanagelist(idtype)
    {
      let projectid=1;
      if(idtype===1)
      {
        projectid=this.project.id
      }
      else if(idtype===2)
      {
        projectid=this.project.affid
      }
      alert(idtype)
      this.opensimulatemanageloading=true;
      getsimulationlistApi(projectid,idtype).then(res => {
             this.opensimulatemanageloading=false;
             if(res.data.status==200)
             {
               this.simulatemanagelist=res.data.data
             }
             else
             {
                this.$message({
                message: res.data.msg,
                type: 'error',
                duration:8000
              });
             }
             
           }).catch(err => {
             this.opensimulatemanageloading=false;
            this.$message({
              message: '服务器错误:'+err,
              type: 'error',
              duration:8000
            });
          })
    },
    //打开备份表数据
    openprojectaff(projectaff){
      this.projectmanageropendialog=false;
      this.openprojectaffloading=true;
      this.projectaffmanageropendialog=true;
      getproaffbyprojectidlistApi(projectaff.projectidPro).then(res=>{
         this.openprojectaffloading=false
        if(res.data.status===200)
        {
            let projectaffArre=[]
            res.data.data.forEach(element => {
              projectaffArre.push({
                projectidProaff:element.projectidProaff,
                createdateProaff:element.createdateProaff,
                prousernameProaff:element.prousernameProaff,
                remarkProaff:element.remarkProaff,
                projectnamePro:projectaff.projectnamePro,
              })
            });
            console.log(projectaffArre)
            this.projectafflist=projectaffArre
        }
        else
        {
           this.$message({
              message: res.data.msg,
              type: 'error',
              duration:8000
            });
        }
        
      }).catch(err => {
         this.openprojectaffloading=false
            this.$message({
              message: '服务器错误:'+err,
              type: 'error',
              duration:8000
          });
        });
    },
    //删除备份表
    deleteprojectaff(projectidproaff)
    {
      if(projectidproaff==this.project.newid)
        {
          this.$message({
            message: '不能删除正在打开备份版本数据！',
            type: 'warning'
          });
          return false;
        }
       this.$confirm("您确定删除该备份数据吗？", '提示', {
                confirmButtonText: '是',
                cancelButtonText: '否',
                type: 'warning'
              }).then(() => {
              this.deleteprojectfun(projectidproaff)  
            }).catch((action) => {
              //if(action === 'cancel')
              //this.projectopenfun(projectid)   
        });
       
    },
    //删除备份表函数
    deleteprojectafffun(projectidproaff)
    {
       deleteprojectaffbyprojectaffidApi(projectidproaff).then(res=>{
        if(res.data.status===200)
        {
          this.openprojectaff(this.project.id);
        }
        else
        {
           this.$message({
                message: res.data.msg,
                type: 'error',
                duration:8000
            });
        }
      }).catch(err => {
            this.$message({
              message: '服务器错误:'+err,
              type: 'error',
              duration:8000
          });
        });
    },
    //删除工程时进行提示
    deleteproject(projectid)
    {
      alert(projectid)
      alert(this.project.id)
        if(projectid==this.project.id)
        {
          this.$message({
            message: '不能删除正在打开的工程！',
            type: 'warning'
          });
          return false;
        }
        proaffcountbyprojectidApi(projectid).then(res=>{
              this.$confirm('该工程包含'+res.data.data+"个版本信息，您确定删除吗？", '提示', {
                confirmButtonText: '是',
                cancelButtonText: '否',
                type: 'warning'
              }).then(() => {
              this.deleteprojectfun(projectid)  
            }).catch((action) => {
              //if(action === 'cancel')
              //this.projectopenfun(projectid)   
            });
        }).catch(err => {
            this.$message({
              message: '服务器错误:'+err,
              type: 'error',
              duration:8000
          });
        });
    },
    //删除工程的函数
    deleteprojectfun(projectid)
    {
      deleteprojectbyprojectidApi(projectid).then(res=>{
        if(res.data.status===200)
        {
          this.getProjectListApi();
        }
        else
        {
           this.$message({
                message: res.data.msg,
                type: 'error',
                duration:8000
              });
        }
      }).catch(err => {
            this.$message({
              message: '服务器错误:'+err,
              type: 'error',
              duration:8000
          });
        });
    },
    //获取工程集合
    getprojectlist()
    {
      getProjectListApi().then(res => {
             this.openprojectloading=false;
             if(res.data.status==200)
             {
               this.projectlist=res.data.data
             }
             else
             {
                this.$message({
                message: res.data.msg,
                type: 'error',
                duration:8000
              });
             }
             
           }).catch(err => {
             this.openprojectloading=false;
            this.$message({
              message: '服务器错误:'+err,
              type: 'error',
              duration:8000
            });
          })
    },
    //保存工程
    saveprojectfun()
    {
        this.$emit("saveprojectmsg")
    },
    //根据备份表ID打开工程
    projectaffidbyopenproject(projectid)
    {
      this.projectaffmanageropendialog=false;
      this.$emit("projectopening",projectid,2)
    },
    //根据工程表ID打开工程
    openprojectfun(projectid)
    {
      this.openprojectopendialog=false
      this.$emit("projectopening",projectid,1)
    },
    //关闭各项弹出框
    closedialog(dialogname)
    {
      //alert(dialogname)
       switch (dialogname) {
        case "newproject":
          this.newprojectopendialog=false;
          break;
        case "openproject":
          this.openprojectopendialog=false;
          break;
        case "simulateset":
          this.simulatesetopendialog=false;
        break;
        default:

          break;
      }
    }
  }
};
</script>