<template>
    <el-form ref="newprojectform" :model="newprojectmodel" :rules="rules" label-width="90px">
      <el-form-item label="工程名称:" prop="projectnamePro">
        <el-input v-model="newprojectmodel.projectnamePro" placeholder="请填写工程名称"></el-input>
      </el-form-item>
    <el-form-item label="描述:">
     <el-input type="textarea" v-model="newprojectmodel.remarkPro"  placeholder="请填写工程描述"></el-input>
    </el-form-item>
     <el-form-item>
      <el-button type="primary" @click="onSubmit">立即创建</el-button>
      <el-button @click="resetForm">重置</el-button>
    </el-form-item>
    </el-form>  
</template>
<script>
import {
  newProjectApi,
} from '@/api/gwwlmxgl'
export default {
  data() {
    return {
      //新建工程的Model
      newprojectmodel:{
        projectnamePro:"",
        remarkPro:""
      },
      //验证
       rules: {
          projectnamePro: [
            { required: true, message: '请填写工程名称！', trigger: 'blur' },
            { min: 3, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
       }
    };
  },
  methods:{
    onSubmit()
    {
      let _this=this;
      console.log(_this)
      this.$refs["newprojectform"].validate((valid) => {
          if (!valid) {
            return false
          }
          newProjectApi(this.newprojectmodel).then(res => {
            console.log(res.data)
            //if()
            if (res.data.status===200)
            {
              //x渲染SVG
              _this.$parent.$parent.$parent.$parent.$parent.projectload(res.data.data.projectid,_this.newprojectmodel.projectnamePro,res.data.data.projectaffid);
              _this.$parent.$parent.closedialog("newproject");
              this.resetForm()
              this.$message({
                message: '创建成功！',
                type: 'success',
                duration:8000
              });
            }
            else
            {
              this.$message({
                message: '创建失败:'+res.data.msg,
                type: 'error',
                duration:8000
              });
            }
          }).catch(err => {
            this.$message({
              message: '服务器错误:'+err,
              type: 'error',
              duration:8000
            });
          })
      })
    },
    resetForm() {
        this.$refs["newprojectform"].resetFields();
      }
  }
}
</script>