import { DEVICE_TYPE, DEVICES } from "./PB_Device";

export module Attribute {
  /////////////////////////////////////////////////////////////////////////////////////////////    interface   ////////
  ////------------------------------------------------键值对接口--------------------------------------------------////
  interface ValueText {
    value: string | number;
    text?: string | number;
  }

  /////////////////////////////////////////////////////////////////////////////////////////////    interface   ////////
  ////------------------------------------Device的attrData属性结构------------------------------------------------////
  /**
   * id:数据库id
   * name:属性名(英文)
   * title:显示名称（中文）
   * value:属性值
   * unit: 量纲单位
   * editable: 是否允许手动编辑
   * type: 类型（直接输入型、选择型）
   * condition: 直接输入型的输入值限定条件（现在还没想好怎么用）
   * store; 选择型的被选项列表
   * show：是否在属性框展示
   * tip: 清空输入时的提示信息，一般为可输入的值域范围信息
   * opendilaog：是否进一步打开弹出框进行编辑
   * remark:描述
   */
  export interface AttrItem {
    id: number;
    name: string;
    title: string | null;
    value: string | number | null;
    // valueId?: string | number | null;
    text?: string;
    unit: string | null;
    editable: boolean;
    inputType: number;
    arcMode: number;
    item?: Array<ValueText>;
    condition?: any;
    show?: boolean;
    tip?: string | null;
    opendilaog?: boolean
    remark?:string
  }

  /////////////////////////////////////////////////////////////////////////////////////////////////    type   ////////
  ////-----------------------------------------------属性表类型--------------------------------------------------////
  export type Attrs = Array<AttrItem>;

  //属性索引->属性值的集合
  export type AttrIndex2ValueMap = Map<ATTR_ENUM, string | number | null>;

  // export function getKeyIndexByAttrName(attrName: string): ATTR_ENUM {
  //     let attrKey: ATTR_ENUM;
  //     for (let i = 0; i < DEFAULT_ATTRIBUTES.length; i++) {
  //         if (DEFAULT_ATTRIBUTES[i].name = attrName) {
  //             attrKey = i;
  //         }
  //     }
  //     return attrKey;
  // }

  export enum ATTR_ENUM {
    _0_Tile,
    _1_PIS_ID,
    _2_TrueName,
    _3_SwitchState,
    _4_EnableState,
    _5_PipeCoefficient,
    _6_Reliability
  }

  /////////////////////////////////////////////////////////////////////////////////////////////////    const   ////////
  ////----------------------------------------属性表默认值总表对象------------------------------------------------////
  export let DEFAULT_ATTRS: Attrs = []

  export enum DEVICE_CLASSIFY_ENUM {
    NormalDevice,
    SwitchDevice,
    StationDevice,
    PLDecice,
    WellDecice,
    HeatDecice
  }

  export const ATTR_CLASSIFY: Map<
    DEVICE_CLASSIFY_ENUM,
    Array<number>
  > = new Map();
  // ATTR_CLASSIFY.set(DEVICE_CLASSIFY_ENUM.NormalDevice, [0, 1, 2, 4,]);
  // ATTR_CLASSIFY.set(DEVICE_CLASSIFY_ENUM.SwitchDevice, [0, 1, 2, 3, 4,]);
  // ATTR_CLASSIFY.set(DEVICE_CLASSIFY_ENUM.StationDevice, [0, 1, 2, 4,]);
  // ATTR_CLASSIFY.set(DEVICE_CLASSIFY_ENUM.PLDecice, [

  // ]);
  export function setATTR_CLASSIFY(AttrList)
  {
    AttrList.forEach(element => {
      switch(element.elementtype)
      {
        case "NormalDevice":
            ATTR_CLASSIFY.set(DEVICE_CLASSIFY_ENUM.NormalDevice, element.arrtid);
            break;
        case "SwitchDevice":
            ATTR_CLASSIFY.set(DEVICE_CLASSIFY_ENUM.SwitchDevice, element.arrtid);
            break;
        case "StationDevice":
            ATTR_CLASSIFY.set(DEVICE_CLASSIFY_ENUM.StationDevice, element.arrtid);
            break;
        case "PLDecice":
            ATTR_CLASSIFY.set(DEVICE_CLASSIFY_ENUM.PLDecice, element.arrtid);
            break;
      }

    });
    console.log(ATTR_CLASSIFY)
  }
  export function getDefaultAttrItem(id: number): AttrItem | null {
    let attritem = DEFAULT_ATTRS.filter(d => {
      if (d.id == id) {
        return true;
      }
    });
    if (attritem.length > 0) return Object.create(attritem[0]);
    return null;
  }
  export function getArcMode(id: number): number {
    let attritem = DEFAULT_ATTRS.filter(d => {
      if (d.id == id) {
        return true;
      }
    });
    return attritem[0].arcMode;
  }
  function _getValueByIndex(id: number, attrs: Attrs): string | number | null {
    let value: string | number | null = null;
    let attritem = attrs.filter(d => {
      if (d.id == id) {
        return true;
      }
    });
    if (attritem.length > 0) {
      value = attritem[0].value;
    }
    return value;
  }
 
  export function getDefault_AIV_Map(
    deviceType: DEVICE_TYPE
  ): AttrIndex2ValueMap {
    console.log(DEVICES)
    let attrIndex2ValueMap = new Map<ATTR_ENUM, string | number | null>(),
      attrClassify = DEVICES.get(deviceType).attrClassify;
    for (let i of ATTR_CLASSIFY.get(attrClassify))
      attrIndex2ValueMap.set(i, _getValueByIndex(i, DEFAULT_ATTRS));
    return attrIndex2ValueMap;
  }
}
