<template>
  <div id="rightMenu">
  <ul>
    <!-- <li><strong>JavaScript 学习</strong></li> -->
     <!-- <li>复制</li> -->
     <div v-for="items in contextitem" :key="items.text" >
      <li  style="" @click="items.handler" v-if="!items.disabled"> {{items.text}}
        <span v-if="items.shortcut">{{items.shortcut}}</span>
        <ul v-if="items.menu">
          <li v-for="item in items.menu" style="" :key="item.text" @click="item.handler"> {{item.text}}</li>
        </ul>
      </li>
    </div>
  </ul>
</div>
</template>
<script>
export default {
  data() {
        
    return {
        i: 0,
        oMenu : [],
        aUl : [],
        aDoc:[],
        maxHeight:0,
        maxWidth:0,
        hideTimer:null,
         contextitem:[]
    };
    
  },
  props:{
    // :{
    //   type:Array
    // }
  },
  mounted()
  {
    this. onload()
  },
  watch:{
    contextitem(val)
    {
      if(val)
      {
        if(val.length>0)
        {
          this.$nextTick(function() {
                  this. onload();
                  this.oMenu.style.display = "block";
          })
        }
        else
        {
           this.oMenu.style.display = "none";
        }
      
      }
      else
      {
        this.oMenu.style.display = "none";
      }

    }
  },
  methods:{
    click()
    {alert("点击")},
    onclose(){
      //alert("关闭")
      this.oMenu.style.display = "none";
      console.log(this.aUl)
    },
    onload()
    {
       let  getOffset ={
          top: function (obj) {
            return obj.offsetTop + (obj.offsetParent ? arguments.callee(obj.offsetParent) : 0) 
          },
          left: function (obj) {
            let ddd=arguments.callee(obj.offsetParent);
            console.log(ddd)
            return obj.offsetLeft + (obj.offsetParent ? arguments.callee(obj.offsetParent) : 0) 
          }	
        }
      this.oMenu = document.getElementById("rightMenu");
       this.aUl = this.oMenu.getElementsByTagName("ul");
      let aLi = this.oMenu.getElementsByTagName("li");
      let showTimer = null;
      this.hideTimer = null;
      this.i = 0;
      this.maxWidth = 0;
      this.maxHeight = 0;
      this.aDoc = [document.documentElement.offsetWidth-300, document.documentElement.offsetHeight-100];	
      this.oMenu.style.display = "none";
      console.log("...")
      for (this.i = 0; this.i < aLi.length; this.i++)
      {
        aLi[this.i].width=100;
        //为含有子菜单的li加上箭头
        aLi[this.i].getElementsByTagName("ul")[0] && (aLi[this.i].className = "sub");
        
        //鼠标移入
        let _this=this;
        aLi[_this.i].onmouseover = function ()
        {
          var oThis = this;
          var oUl = oThis.getElementsByTagName("ul");
          
          //鼠标移入样式
          oThis.className += " active";			
          
          //显示子菜单
          if (oUl[0])
          {
            clearTimeout(_this.hideTimer);				
            showTimer = setTimeout(function ()
            {
              for (_this.i = 0; _this.i < oThis.parentNode.children.length; _this.i++)
              {
                oThis.parentNode.children[_this.i].getElementsByTagName("ul")[0] &&
                (oThis.parentNode.children[_this.i].getElementsByTagName("ul")[0].style.display = "none");
              }
              oUl[0].style.display = "block";
              oUl[0].style.top = oThis.offsetTop + "px";
              oUl[0].style.left = oThis.offsetWidth + "px";
              _this.setWidth(oUl[0]);
              
              //最大显示范围					
              _this.maxWidth = _this.aDoc[0] - oUl[0].offsetWidth;
              _this.maxHeight = _this.aDoc[1] - oUl[0].offsetHeight;
              
              //防止溢出
              _this.maxWidth < getOffset.left(oUl[0]) && (oUl[0].style.left = -oUl[0].clientWidth + "px");
              _this.maxHeight < getOffset.top(oUl[0]) && (oUl[0].style.top = -oUl[0].clientHeight + oThis.offsetTop + oThis.clientHeight + "px")
            },300);
          }			
        };
        
        //鼠标移出	
        aLi[_this.i].onmouseout = function ()
        {
          var oThis = this;
          var oUl = oThis.getElementsByTagName("ul");
          //鼠标移出样式
          oThis.className = oThis.className.replace(/\s?active/,"");
          
          clearTimeout(showTimer);
          _this.hideTimer = setTimeout(function ()
          {
            for (_this.i = 0; _this.i < oThis.parentNode.children.length; _this.i++)
            {
              oThis.parentNode.children[_this.i].getElementsByTagName("ul")[0] &&
              (oThis.parentNode.children[_this.i].getElementsByTagName("ul")[0].style.display = "none");
            }
          },300);
        };
      }	
    },
    oncontextmenu(event,items)
    {
        console.log(items)
        this.contextitem=items
        //let event = event || window.event;
        //alert("2")
        this.oMenu.style.display = "block";
        this.oMenu.style.top = event.clientY + "px";
        this.oMenu.style.left = event.clientX + "px";
        this.setWidth(this.aUl[0]);
        
        //最大显示范围
        this.maxWidth = this.aDoc[0] - this.oMenu.offsetWidth;
        this.maxHeight = this.aDoc[1] - this.oMenu.offsetHeight;
        
        //防止菜单溢出
        this.oMenu.offsetTop > this.maxHeight && (this.oMenu.style.top = this.maxHeight + "px");
        this.oMenu.offsetLeft > this.maxWidth && (this.oMenu.style.left = this.maxWidth + "px");
        return false;
    },
    setWidth(obj)
    {
      this.maxWidth = 0;
      for (this.i = 0; this.i < obj.children.length; this.i++)
      {
        var oLi = obj.children[this.i];			
        var iWidth = oLi.clientWidth - parseInt(oLi.currentStyle ? oLi.currentStyle["paddingLeft"] : getComputedStyle(oLi,null)["paddingLeft"]) * 2
        if (iWidth >  this.maxWidth)  this.maxWidth = iWidth;
      }
      for (this.i = 0; this.i < obj.children.length; this.i++) obj.children[this.i].style.width =  this.maxWidth + "px";
    }
  }
}
</script>
<style scoped>
@charset "utf-8";
/* 代码整理：懒人之家 lanrenzhijia.com */
html, body {
	height:100%;
	overflow:hidden;
}
body, div, ul, li {
	margin:0;
	padding:0;
}
body {
	font:12px/1.5 \5fae\8f6f\96c5\9ed1;
}
ul {
	list-style-type:none;
}
#rightMenu {
	position:absolute;
	top:-9999px;
	left:-9999px;
}
#rightMenu ul {
	float:left;
	border:1px solid #979797;
	background:url(/content/element_icons/line.png) 24px 0 repeat-y #f1f1f1;
	padding:2px;
	box-shadow:2px 2px 2px rgba(0, 0, 0, .6);
}
#rightMenu ul li {
	float:left;
	clear:both;
	height:24px;
  width: 100px;
	cursor:pointer;
	line-height:24px;
	white-space:nowrap;
	padding:0 30px;
}
#rightMenu ul li.sub {
	background-repeat:no-repeat;
	background-position:right 9px;
	background-image:url(/content/element_icons/arrow.png);
}
#rightMenu ul li.active {
	background-color:#f1f3f6;
	border-radius:3px;
	border:1px solid #aecff7;
	height:22px;
	line-height:22px;
	background-position:right -8px;
	padding:0 29px;
}
#rightMenu ul ul {
	display:none;
	position:absolute;
}

</style>