/**
 * 获取屏幕坐标X(相对于画布原点，而非真正的屏幕原点或浏览器原点)
 * @returns {number}
 */
export function screenX(): number {
    return d3.event.layerX;
}

/**
 * 获取屏幕坐标Y(相对于画布原点，而非真正的屏幕原点或浏览器原点)
 * @returns {number}
 */
export function screenY(): number {
    return d3.event.layerY;
}

/**
 * 获取屏幕坐标(相对于画布原点，而非真正的屏幕原点或浏览器原点)
 * @returns {{x: number, y: number}} 鼠标所在屏幕坐标
 */
export function screenCoordinate(): Point {
    return {x: d3.event.layerX, y: d3.event.layerY};
}


/**
 * 返回当前鼠标按键值
 * @returns {number}   0、没有按键
 *                      1、左键
 *                      2、右键
 *                      4、中键
 */
export function mouseEventBtn(): number {

    let btn: number = d3.event.button;
    let s: number = d3.event.buttons;

    if (s)
        return s;
    else if (btn) {
        switch (btn) {
            case 1:
                return 4;
            default:
                return 2;
            //case 2: return 2;
        }
    }
    else
        return d3.event.type=='mouseup'?1:0; //左键mouseup的时候跟平常不按任何建的值在event中一样，所以要额外判断

}




