<template>
  <el-table
    :data="projectafflist"
    v-loading="loading"
    border
    stripe
    center
    size="small"
    max-height="400"
    style="width: 100%">
    <el-table-column
      fixed
      prop="projectnamePro"
      label="工程名称"
       align="center"
      min-width="150">
    </el-table-column>
    <el-table-column
      prop="createdateProaff"
      label="备份时间"
       align="center"
      min-width="150">
    </el-table-column>
    <el-table-column
      prop="prousernameProaff"
      label="备份用户名"
      align="center"
      min-width="120">
    </el-table-column>
    <el-table-column
     show-overflow-tooltip
      prop="remarkProaff"
       align="center"
      label="备注"
      min-width="120">
    </el-table-column>
    <el-table-column
      fixed="right"
      align="center"
      label="操作"
      min-width="160">
      <template slot-scope="scope">
        <el-button @click="handleDeleteClick(scope.row)" type="text" size="small">删除</el-button>
        <el-button @click="handleClick(scope.row)" type="text" size="small">打开</el-button>
        <!-- <el-button type="text" size="small">编辑</el-button> -->
      </template>
    </el-table-column>
  </el-table>
</template>
<script>
export default {
methods: {
      handleDeleteClick(row) {
         this.$emit('deleteprojectaff',row.projectidProaff);
      },
      handleClick(row) 
      {
         this.$emit('projectaffidbyopenproject',row.projectidProaff);
      }
    },
    props:{
      projectafflist:{
        type:Array
      },
      loading:{
        type:Boolean,
        default:false
      }
    },
    watch:{
    },
    data() {
      return {
        
      }
  }
}
</script>