<template>
<div>
  <el-radio-group v-model="selectradio" size="small" @change="selectradiochange">
    <el-radio  label="2">当前的仿真模拟方案</el-radio>
    <el-radio  label="1">当前工程的仿真模拟方案</el-radio>
    <el-radio  label="3">所有的仿真模拟方案</el-radio>
  </el-radio-group>
  <el-table
    :data="simulatemanagelist"
    v-loading="loading"
    border
    stripe
    center
    size="small"
    max-height="400"
    style="width: 100%">
     <el-table-column
      fixed
      prop="projectnamePro"
      label="工程名称"
      align="center"
      min-width="150">
    </el-table-column>
      <el-table-column
      prop="createdateProaff"
      label="工程备份时间"
      align="center"
      min-width="150">
    </el-table-column>
     <el-table-column
      prop="simulationnameSim"
      label="方案名称"
      align="center"
      min-width="150">
    </el-table-column>
    <el-table-column
      prop="simsourceSim"
      label="方案来源"
       align="center"
      min-width="120">
    </el-table-column>
    <el-table-column
      prop="simusernameSim"
      label="用户名"
       align="center"
      min-width="180">
    </el-table-column>
    <el-table-column
     show-overflow-tooltip
      prop="createdateSim"
       align="center"
      label="创建时间"
      min-width="120">
    </el-table-column>
    <el-table-column
     show-overflow-tooltip
      prop="remarkSim"
      align="center"
      label="备注"
      min-width="120">
    </el-table-column>
    <el-table-column
      fixed="right"
      align="center"
      label="操作"
      min-width="160">
      <template slot-scope="scope">
        <el-button @click="handleDeleteClick(scope.row)" type="text" size="small">删除</el-button>
        <el-button @click="handleOpenClick(scope.row)" type="text" size="small">备份信息</el-button>
        <!-- <el-button type="text" size="small">编辑</el-button> -->
      </template>
    </el-table-column>
  </el-table>
</div>
</template>
<script>
export default {
data(){
  return{
    selectradio:"2"
  }
},
methods: {
      handleDeleteClick(row) {
         this.$emit('deletesimulatemanage',row.simulationidSim);
      },
      handleOpenClick(row){
         this.$emit('openprojectaff',row);
      },
      selectradiochange()
      {
        this.$emit('getsimulatemanagelist',this.selectradio);
      }
    },
    props:{
      simulatemanagelist:{
        type:Array
      },
      loading:{
        type:Boolean,
        default:false
      }
    }
}
</script>