import { Arc } from "./PB_Arc";
import { DeviceManager } from "./PB_DeviceManager";
import { copyMap } from "./PB_Tools";
import { Device } from "./PB_Device";
import { Attribute } from "./PB_Attribute";
import ATTR_ENUM = Attribute.ATTR_ENUM;
import AIV_Map = Attribute.AttrIndex2ValueMap;

export class ArcManager {
  static st_instance: ArcManager = null;

  //新建工况对话框函数
  static st_PopNewArcDlg: Function;
  //树控件中删除某个工况的函数
  static st_DelArcFromTree: Function;
  //树控件添加节点
  static st_Pop_newArc: Function;
  /**
   * 创建单例
   * @returns {ArcManager}
   */
  static getInstance() {
    if (!ArcManager.st_instance) ArcManager.st_instance = new ArcManager();
    return ArcManager.st_instance;
  }

  //工况ID->工况集合
  private arcId2ArcMap: Map<number, Arc>;

  //当前选中工况
  private currentArc: Arc;

  /**
   * 构造器默认不可外部访问，不可new
   */
  private constructor() {
    this.arcId2ArcMap = new Map<number, Arc>();
    this.newArc(0, "默认工况", "默认工况", true);
    this.currentArc = this.arcId2ArcMap.get(this.getArcByName("默认工况").getId());
  }

  public getId2ArcMap(): Map<number, Arc> {
    return this.arcId2ArcMap;
  }

  /**
   * 将工况增加到工况ID中
   * @param arc 工况
   * @constructor
   */
  addArcToMap(arc: Arc): void {
    this.arcId2ArcMap.set(arc.getId(), arc);
    //console.log(this.arcId2ArcMap)
  }

  /**
   * 从this.arcId2ArcMap集合中根据工况名称移除相应工况
   * @param arcName 工况名称
   * @constructor
   */
  removeArcByArcName(arcName: string): void {
    this.arcId2ArcMap.forEach((arc, arcId) => {
      if (arc.getName() == arcName) this.arcId2ArcMap.delete(arcId);
    });
  }

  /**
   * 工况是否已存在
   * @param arcName 工况名称
   * @returns {boolean}
   */
  isArcExist(arcName: string): boolean {
    let bRet: boolean = false;
    this.arcId2ArcMap.forEach(arc => {
      if (arcName == arc.getName()) bRet = true;
    });
    return bRet;
  }

  /**
   * 通过工况名称获取工况
   * @param name 工况名称
   * @returns {Arc} 工况
   */
  getArcByName(name: string): Arc | null {
    let arc: Arc = null;
    this.arcId2ArcMap.forEach(value => {
      if (name == value.getName()) arc = value;
    });

    return arc;
  }

  /**
   * 根据工况名称从内存中加载工况到当前工程中的元件上
   * 这个需要考虑工程中后来元件数量的变动造成的影响
   * @param name 工况名称
   */
  loadArc2DeviceByName(name: string): void {
    let arc: Arc = this.getArcByName(name);
    arc.loadArcToDevice();
    this.currentArc = arc;
  }

  /**
   * 修改指定的工况中相关元件的属性
   * @param deviceId 元件ID
   * @param key  属性键
   * @param newValue 属性值
   */
  setCurrentArcAttr(
    deviceId: string,
    key: number,
    newValue: string | number | null
  ): void {
    let arcMode = Attribute.getArcMode(key);
    let trueArc = this.currentArc;
    if (!arcMode) trueArc = this.arcId2ArcMap.get(this.getArcByName("默认工况").getId());
    trueArc.setCurArcByDeviceIdAndAttrKey(deviceId, key, newValue);
  }

  /**
   * 向所有的工况中增加新的元件属性
   */
  addNewDeviceToAllArc(device: Device): void {
    this.arcId2ArcMap.forEach(arc => {
      arc.addOrModifyDeviceAndAttributeToArc(
        device.id,
        device.pick_AIV_MapByArcMode(1)
      );
    });
    this.arcId2ArcMap
      .get(this.getArcByName("默认工况").getId())
      .addOrModifyDeviceAndAttributeToArc(device.id, device.get_AIV_Map());
  }

  /**
   * 从所有的工况中删除某个元件
   * @param deviceId  元件ID
   */
  removeDeviceFromAllArc(deviceId: string) {
    this.arcId2ArcMap.forEach(arc => {
      arc.removeDeviceFromArc(deviceId);
    });
  }

  /**
   *  新建工况
   * @param id
   * @param arcName
   * @param isDefaultArc  是否为默认工况，0为非默认工况
   */
  newArc(
    id: number | null,
    arcName: string,
    arcDescribe: string,
    isDefaultArc: boolean = false,
    device2AttrsMap?:Map<string, AIV_Map>
  ): Arc {
    if(!device2AttrsMap)
    {
      device2AttrsMap= new Map<string, AIV_Map>();
      let deviceMap = DeviceManager.getInstance().getDeviceMap();
      console.log("device2AttrsMap")
      deviceMap.forEach((device, deviceId) => {
        //创建默认工况
        if (isDefaultArc)
          device2AttrsMap.set(deviceId, copyMap(device.attrIndex2ValueMap));
        //创建非默认工况
        else {
          device2AttrsMap.set(deviceId, device.pick_AIV_MapByArcMode(1));
        }
      });
    }
    console.log("111111111111111")
    console.log(device2AttrsMap)
    console.log("111111111111111")
    let newArc: Arc = new Arc(id, arcName, arcDescribe, device2AttrsMap);
    this.addArcToMap(newArc);

    return newArc;
  }

  public getJsonObjectArray(): [any] {
    let arr: any = [];
    this.arcId2ArcMap.forEach(arc => {
      arr.push(arc.getJsonObject());
    });
    return arr;
  }

  public setJsonArray(array: [any]): void {
    console.log(array)
   
    
    for (let i of array) {
      let device2AttrsMap:Map<string, AIV_Map>=new Map<string, AIV_Map>();

      i.device.forEach(element => {
        let newMap = new Map()
        element.attr.forEach(elementattr => {
          newMap.set(elementattr.k,elementattr.v)
        });
        device2AttrsMap.set(element.id,newMap)
      });
      this.arcId2ArcMap.set(
        i.id,
        this.newArc(
          i.id,
          i.name,
          i.arcDescribe,
          i.name == "默认工况" ? true : false,
          device2AttrsMap
        )
      );
    }
    let arc: Arc = this.getArcByName("默认工况");
    arc.loadArcToDevice();
  }
  public removeAll(): void {
    this.arcId2ArcMap.clear();
  }
}
