import { SvgLayer } from "./PB_SvgLayer";
import * as d3 from "d3-selection";
import { StateManager } from "./PB_StateManager";
import * as G from "./PB_GlobeVar";
import { Device } from "./PB_Device";
import { ContextMenu } from "./PB_ContextMenu";

/////////////////////////////////////////////////////////////////////////////////////////////////    interface   ////////
////-------------------------------------------------坐标点---------------------------------------------------------////
export interface Point {
  x: number;
  y: number;
}
/////////////////////////////////////////////////////////////////////////////////////////////////    interface   ////////
////-------------------------------------------------保存时获取节点---------------------------------------------------------////
export interface getPoint {
  svgLayerid: string;
  x: number;
  y: number;
}
/////////////////////////////////////////////////////////////////////////////////////////////////    interface   ////////
////-----------------------------------------------图形选中框参数---------------------------------------------------////
export interface AidGraphicArg {
  dx: number;
  dy: number;
  width: number;
  height: number;
}

/////////////////////////////////////////////////////////////////////////////////////////////////    CLASS   ////////
////-------------------------------------边界，两个坐标构成的坐标范围-------------------------------------------////
export class Extend {
  minX: number;
  minY: number;
  maxX: number;
  maxY: number;

  constructor(coordinateMin: Point, coordinateMax: Point) {
    this.minX = coordinateMin.x;
    this.minY = coordinateMin.y;
    this.maxX = coordinateMax.x;
    this.maxY = coordinateMax.y;
  }

  /**
   * 判断是否包含另一个边界
   * @param subExtend  带判断的被包含的边界
   * @returns {boolean}
   */
  public contain(subExtend: Extend): boolean {
    if (
      this.minX <= subExtend.minX &&
      this.maxX >= subExtend.maxX &&
      this.minY <= subExtend.minY &&
      this.maxY >= subExtend.maxY
    )
      return true;
  }
}

/////////////////////////////////////////////////////////////////////////////////////////////////    CLASS   ////////
////----------------------------------------Node、Line、Core的基础类型------------------------------------------////
abstract class Shape {
  id?: string;
  //所属图层
  svgLayer: SvgLayer;
  //图形主体
  mainGraphic: d3.Selection<any, any, any, any>;
  //选中时的辅助图形
  aidGraphic: d3.Selection<any, any, any, any>;
  //图形标题
  textGraphic?: d3.Selection<any, any, any, any>;

  /**
   * Shape类 默认构造器
   * @param id 当前id
   * @param svgLayer 当前svg层
   */
  constructor(id: string | null, svgLayer: SvgLayer) {
    this.id = id;
    this.svgLayer = svgLayer;
  }

  /**
   *  绘制到svg(用于子类复写的方法)
   */
  protected abstract _drawGraphic(): void;

  /**
   * 绘制到svg(实际调用的方法)
   */
  protected drawGraphic(): void {
    this._drawGraphic();
    this.addEventListener();
  }

  protected abstract addEventListener(): void;

  protected abstract movePure(dx: number, dy: number, position: number): void;

  protected abstract moveToPure(point: Point, position: number): void;

  public abstract remove(): void;
}

/////////////////////////////////////////////////////////////////////////////////////////////////    enum   ////////
////--------------------------------------节点关联状态枚举-----------------------------------------------------////
enum RelateState {
  InnerNode4Relate,
  OutNode4Relate,
  InnerNodeRelated,
  OutNodeRelated
}

/////////////////////////////////////////////////////////////////////////////////////////////////    CLASS   ////////
////---------------------------------------------关联节点-------------------------------------------------------////
export class Node extends Shape implements Point {
  //Node类的自增ID，用来定义缺省创建的Node对象的id
  static st_lastID = 0;

  /**
   * 设置当前环境中最大NodeID，用以读取老工程时使用
   * @param maxNumber 当前Node最大值
   */
  static setCurrentMaxID(maxNumber: number) {
    Node.st_lastID = maxNumber;
  }

  x: number;
  y: number;

  /**
   * 关联Device的位置信息
   * key:device的id
   * value:该Node在Device中的位置
   */
  devicePosMap: Map<string, number>;

  //关联图标
  relateMarkGraphic: d3.Selection<any, any, any, any>;

  //当前关联状态
  relateState: RelateState;

  //关联的元件
  relatedDevice: Device;

  //关联节点
  relatedNode: Node;

  constructor(svgLayer: SvgLayer, point: Point, id?: string | null) {
    id == null ? (id = "ND" + Node.st_lastID) : null;
    super(id, svgLayer);

    this.x = point.x;
    this.y = point.y;

    this.devicePosMap = new Map();

    this.drawGraphic();
    this.drawTextGraphic();

    Node.st_lastID++;
  }

  /**
   * 将图形绘制到svglayer
   */
  protected _drawGraphic() {
    this.mainGraphic = (this.svgLayer.graphicLayer_mainLayer as d3.Selection<
      any,
      any,
      any,
      any
    >).append("circle");
    this.mainGraphic
      .attr("cx", this.x)
      .attr("cy", this.y)
      .attr("r", 4);
  }

  protected addEventListener(): void {
    let SM = StateManager.getInstance();

    this.mainGraphic
      .on("mousedown", () => {
        d3.event.preventDefault();
        SM.setMouseDownDeviceOrNode(this);

        if (SM.isCapturingNode() && G.mouseEventBtn() == 1) {
          SM.setNodeCaptured(this, 1);
        } else {
          //将事件及触发该事件的node节点传递到svglayer去处理。这种处理导致svglayer里的else if不好写,故废弃
          //d3.event.nodeSelected = __this;
          this.select();
        }
      })
      .on("mouseover", () => {
        d3.event.stopImmediatePropagation();
        d3.event.preventDefault();

        //绘制元件时鼠标悬停捕捉节点
        if (SM.isCapturingNode()) {
          //给绘制临时线用的,同时用作mouseup时捕捉到第二点赋值操作
          SM.setNodeCaptured(this, 2);
          this.highlight();
        }
      })
      .on("mouseout", () => {
        d3.event.stopImmediatePropagation();
        d3.event.preventDefault();
        //绘制元件时鼠标悬停移出后恢复原有样式
        if (SM.isCapturingNode()) {
          SM.setNodeCaptured(null, 2);
          this.cancelHighlight();
        }
      })
      .on("mouseup", () => {
        if (
          !SM.getType2draw() &&
          (G.mouseEventBtn() == 1 || G.mouseEventBtn() == 2) &&
          !SM.isDeviceMoved
        ) {
          this.select();
          //Node节点右键菜单
          if (G.mouseEventBtn() == 2) {
            console.log("Node节点右键菜单")
            ContextMenu.st_popContextMenu(
              d3.event,
              ContextMenu.st_createNodeContextMenu(this)
            );
          }
        }
      });
  }

  /**
   * 绘制标题文字
   */
  drawTextGraphic() {
    this.textGraphic = this.svgLayer.graphicLayer_textLayer_nodeTextLayer
      .append("text")
      .attr("x", this.x)
      .attr("y", this.y)
      .attr("text-anchor", "middle")
      .attr("dy", -10)
      .text(this.id);
  }

  /**
   * 将选中状态辅助图形绘制在svg上
   */
  public showAidGraphic() {
    if (!this.aidGraphic)
      this.aidGraphic = this.svgLayer.graphicLayer_aidGraphicLayer
        .append("rect")
        .attr("transform", "translate(" + -7 + "," + -7 + ")")
        .attr("x", this.x)
        .attr("y", this.y)
        .attr("width", 14)
        .attr("height", 14)
        .attr("fill", "none")
        .attr("stroke", "red")
        .attr("stroke-width", "1");
  }

  /**
   * 清除选中状态辅助图形
   */
  public hideAidGraphic() {
    if (this.aidGraphic) this.aidGraphic.remove();
    this.aidGraphic = null;
    StateManager.getInstance().nodeSelected = null;
  }

  /**
   * 移动(附带联动关联的元素)
   * @param dx  x轴偏移量
   * @param dy  y轴偏移量
   * @param ignoreID  忽略该节点关联的id为 ignoreID 的元件
   */
  public move(dx: number, dy: number, ignoreID?: string) {
    this.movePure(dx, dy);

    this.devicePosMap.forEach((pos, deviceID) => {
      if (ignoreID == deviceID) return;

      this.svgLayer.deviceMap.get(deviceID).moveNodeLinks(dx, dy, pos);
    });
  }

  /**
   * 移动到point所在位置
   * @param point
   * @param ignoreID 排除移动的device
   */
  public moveTo(point: Point, ignoreID?: string) {
    this.moveToPure(point);
    this.devicePosMap.forEach((pos, deviceID) => {
      if (ignoreID == deviceID) return;
      this.svgLayer.deviceMap.get(deviceID).moveToNodeLinks(point, pos);
    });
  }

  /**
   * 纯移动当前元素，不附带移动其关联部分
   * @param dx  x轴偏移量
   * @param dy  y轴偏移量
   */
  movePure(dx: number, dy: number) {
    this.moveToPure({ x: this.x + dx, y: this.y + dy });
  }

  /**
   * 纯移动，不带动关联部分
   * @param point
   */
  public moveToPure(point: Point) {
    this.x = point.x;
    this.y = point.y;
    this.mainGraphic.attr("cx", this.x).attr("cy", this.y);
    if (this.textGraphic) this.textGraphic.attr("x", this.x).attr("y", this.y);
    if (this.aidGraphic) this.aidGraphic.attr("x", this.x).attr("y", this.y);
    if (this.relateMarkGraphic)
      this.relateMarkGraphic.attr("x", this.x - 16).attr("y", this.y);
  }

  /**
   * 变为高亮样式
   */
  public highlight() {
    this.mainGraphic.style("fill", "#ff6a00").attr("r", 7);
  }

  /**
   * 取消高亮样式
   */
  public cancelHighlight() {
    this.mainGraphic.style("fill", "").attr("r", 4);
  }

  /**
   * 关联元件
   * @param deviceID  元件id
   * @param position  该node在device上的位置，1 或 2
   */
  linkDevice(deviceID: string, position: number) {
    console.log("linkDevice")
    this.devicePosMap.set(deviceID, position);
  }

  /**
   * 将一个以存在的节点关联到一个新的Device，并且重新绘制Node，以防止被line覆盖
   * @param deviceID 新的Device
   * @param position 该node在新Device上的位置
   */
  redrawAndLinkDevice(deviceID: string, position: number) {
    this.mainGraphic.remove();
    this.textGraphic.remove();
    this.drawGraphic();
    this.drawTextGraphic();
    this.linkDevice(deviceID, position);
  }

  /**
   * 将device从当前node的关联记录中移除
   * @param id 待移除的device的id
   * @returns {boolean}  是否已经移除所有Device
   */
  public removeRecordFromDevicePosMapByID(id: string): boolean {
    this.devicePosMap.delete(id);
    if (!this.devicePosMap.size) {
      this.remove();
      return true;
    }
    return false;
  }

  /**
   * 移除、删除
   */
  public remove() {
    //清除关联信息20170412,kid
    // if (this.linkState) {
    //     if (this.linkState.indexOf('4Link') != -1)
    //         this.removeLinkLayerContainer();
    //     else if (this.linkState.indexOf('Linked') != -1)
    //         this.removeLinkNodeAndLayerContainer();
    // }
    this.devicePosMap.clear();

    if (this.mainGraphic) this.mainGraphic.remove();
    this.mainGraphic = null;
    if (this.textGraphic) this.textGraphic.remove();
    this.textGraphic = null;
    this.hideAidGraphic();
    //this.hideLinkMarkGraphic();

    //清除svglayer中的NodeSet索引
    this.svgLayer.nodeSet.delete(this);
  }

  /**
   * 选中
   */
  select() {
    let SM = StateManager.getInstance();
    SM.clearAllAidGraphic();
    SM.nodeSelected = this; ////////////////这在框选的时候会不会有问题???????
    this.showAidGraphic();
  }

  public setRelatedDevice(device: Device, isNodeInDevice: boolean = false) {
    this.relatedDevice = device;
    this.relateState = isNodeInDevice
      ? RelateState.InnerNode4Relate
      : RelateState.OutNode4Relate;
    this.showRelateMarkGraphic();
  }

  public setRelatedNode(node: Node) {
    this.relatedNode = node;
    this.relateState =
      this.relateState == RelateState.InnerNode4Relate
        ? RelateState.InnerNodeRelated
        : RelateState.OutNodeRelated;
    this.showRelateMarkGraphic();
  }

  private showRelateMarkGraphic() {
    if (this.relateMarkGraphic) this.hideRelateMarkGraphic();

    this.relateMarkGraphic = this.svgLayer.graphicLayer_textLayer_otherTextLayer
      .append("use")
      .attr("xlink:href", "#pb_svgDef_" + RelateState[this.relateState])
      .attr("x", this.x - 16)
      .attr("y", this.y);
  }

  private hideRelateMarkGraphic() {
    if (this.relateMarkGraphic) {
      this.relateMarkGraphic.remove();
      this.relateMarkGraphic = null;
    }
  }
}

/////////////////////////////////////////////////////////////////////////////////////////////////    CLASS   ////////
////---------------------------------------------------线-------------------------------------------------------////
export class Line extends Shape {
  p1: Point;
  p2: Point;
  deviceID: string;

  /**
   * Line的构造函数
   * @param svgLayer  svgLayer对象
   * @param p1  起始坐标
   * @param p2  终止坐标
   * @param deviceID  关联的元件ID
   */
  constructor(svgLayer: SvgLayer, p1: Point, p2: Point, deviceID: string) {
    super(null, svgLayer);

    //这回来个大坑，如果这么直接传的话，传进来的有可能是node对象，导致p为node的对象。
    // 这样，对line进行移动时候，原本只想改变p的值，结果导致node也被修改
    //this.p1 = p1;
    //this.p2 = p2;

    //下面这种方式应该也行
    // let x1,x2,y1,y2;
    // x1=p1.x;
    // x2=p2.x;
    // y1=p1.y;
    // y2=p2.y;

    //利用函数参数的形式，强制进行“值传递”
    function temp_newPoint(x: number, y: number) {
      return { x: x, y: y };
    }

    this.p1 = temp_newPoint(p1.x, p1.y);
    this.p2 = temp_newPoint(p2.x, p2.y);
    this.deviceID = deviceID;
    this.drawGraphic();
  }

  /**
   * 将图形绘制到svglayer
   */
  protected _drawGraphic() {
    this.mainGraphic = (this.svgLayer.graphicLayer_mainLayer as d3.Selection<
      any,
      any,
      any,
      any
    >).append("g");

    //扩展线条，两侧留白，但是导致Node香蕉的地方角度小时会遮挡，所以留的级细
    this.mainGraphic
      .append("line")
      .attr("x1", this.p1.x)
      .attr("y1", this.p1.y)
      .attr("x2", this.p2.x)
      .attr("y2", this.p2.y)
      .attr("stroke", "#FFF")
      .attr("stroke-width", "5");
    this.mainGraphic
      .append("line")
      .attr("x1", this.p1.x)
      .attr("y1", this.p1.y)
      .attr("x2", this.p2.x)
      .attr("y2", this.p2.y)
      .attr("stroke", "#555")
      .attr("stroke-width", "2");
  }

  /**
   * 添加鼠标事件侦听
   */
  protected addEventListener() {
    let sm = StateManager.getInstance();

    this.mainGraphic
      .on("mousedown", () => {
        if (
          !sm.getType2draw() &&
          (G.mouseEventBtn() == 1 || G.mouseEventBtn() == 2)
        ) {
          this.svgLayer.deviceMap.get(this.deviceID).doMouseDownLine();
        }
      })
      .on("mouseup", () => {
        //if (!G.elementType2draw && !event.button) {

        this.svgLayer.deviceMap.get(this.deviceID).doMouseUpLine();

        //}
      })
      .on("dblclick", () => {
        this.svgLayer.deviceMap.get(this.deviceID).doDoubleClick();
      });
  }

  /**
   * 纯移动，不移动关联元素
   * @param dx  x轴移动量
   * @param dy  Y轴移动量
   * @param position  移动的位置，  1   ：移动起点，
   *                                 2   ：移动终点，
   *                               不传值：两点都移动
   */
  public movePure(dx: number, dy: number, position?: number) {
    //临时模板函数
    function temp_movePure(
      dx: number,
      dy: number,
      point: Point,
      position: number,
      mainGraphic: d3.Selection<any, any, any, any>
    ) {
      point.x = point.x + dx;
      point.y = point.y + dy;
      mainGraphic
        .selectAll("line")
        .attr("x" + position, point.x)
        .attr("y" + position, point.y);
    }

    if (position == 1) {
      temp_movePure(dx, dy, this.p1, 1, this.mainGraphic);
    } else if (position == 2) {
      temp_movePure(dx, dy, this.p2, 2, this.mainGraphic);
    } else {
      temp_movePure(dx, dy, this.p1, 1, this.mainGraphic);
      temp_movePure(dx, dy, this.p2, 2, this.mainGraphic);
    }
  }

  /**
   * 纯移动到point所在位置
   * @param point
   * @param position 移动line的哪个点，1：p1、2:p2
   */
  public moveToPure(point: Point, position: number) {
    //这一句代码差点没给我搞死，一周末就调试他了。
    // javascript“看起来像的的东西可以直接用”的大坑啊。!!!!!
    // 以后一定注意这样传进来的东西是引用传递，如果有交叉调用，很容易出现逻辑错误!!!!!!!!!!
    // position == 1 ? this.p1 = point : this.p2 = point;
    position == 1
      ? (this.p1 = { x: point.x, y: point.y })
      : (this.p2 = { x: point.x, y: point.y });
    this.mainGraphic
      .selectAll("line")
      .attr("x" + position, point.x)
      .attr("y" + position, point.y);
  }

  /**
   * 移除、删除
   */
  public remove() {
    this.p1 = null;
    this.p2 = null;
    if (this.mainGraphic) this.mainGraphic.remove();
    this.mainGraphic = null;
    this.id = null;
  }
}

/////////////////////////////////////////////////////////////////////////////////////////////////    CLASS   ////////
////--------------------------------------------元件的核心------------------------------------------------------////
export class Core extends Shape implements Point {
  x: number;
  y: number;

  deviceID: string;
  rotate: number;
  coreType: string;
  aidGraphicArg: AidGraphicArg;
  title: string;
  textDy: number;

  /**
   *Core类 构造函数
   * @param deviceID 所属device的id
   * @param svgLayer 所在svgLayer
   * @param point   图形中心位置
   * @param rotate  图形的旋转角度
   * @param deviceType  元件类型
   * @param aidGraphicArg  元件虚线框的参数
   * @param title  元件显示标题
   * @param textDy  标题纵向偏移量
   */
  constructor(
    deviceID: string,
    svgLayer: SvgLayer,
    point: Point,
    rotate: number,
    deviceType: string,
    aidGraphicArg: AidGraphicArg = { dx: 0, dy: 0, width: 0, height: 0 },
    textDy: number = 25,
    title: string
  ) {
    super(deviceID + "_core", svgLayer);
    this.x = point.x;
    this.y = point.y;
    this.deviceID = deviceID;
    this.rotate = rotate;
    this.coreType = deviceType;
    this.aidGraphicArg = aidGraphicArg;
    this.title = title ? title : deviceID;
    this.textDy = textDy;

    this.drawGraphic();
    this.drawTextGraphic();
  }

  /**
   * 将图形绘制到svglayer
   */
  protected _drawGraphic() {
    this.mainGraphic = this.svgLayer.graphicLayer_mainLayer
      .append("use")
      .attr("xlink:href", "#pb_svgDef_" + this.coreType)
      .attr("x", this.x)
      .attr("y", this.y)
      .attr(
        "transform",
        "rotate(" + this.rotate + "," + this.x + "," + this.y + ")"
      );
  }

  /**
   * 更改显示状态，0：关闭着的
   *               1：禁用着的
   *               2：打开着的
   * @param state
   */
  public setState(state: number) {
    switch (state) {
      case 1:
        this.mainGraphic.attr(
          "xlink:href",
          "#pb_svgDef_" + this.coreType + "_disabled"
        );
        break;
      case 2:
        this.mainGraphic.attr(
          "xlink:href",
          "#pb_svgDef_" + this.coreType + "_opened"
        );
        break;
      default:
        this.mainGraphic.attr("xlink:href", "#pb_svgDef_" + this.coreType);
    }
  }

  /**
   * 更改标题
   * @param newTitle
   */
  public setTitle(newTitle: string) {
    this.title = newTitle && newTitle != "" ? newTitle : this.deviceID;
    this.textGraphic.text(this.title);
  }

  /**
   * 添加事件侦听
   */
  protected addEventListener() {
    let sm = StateManager.getInstance();

    this.mainGraphic
      .on("mousedown", () => {
        if (
          !sm.getType2draw() &&
          (G.mouseEventBtn() == 1 || G.mouseEventBtn() == 2)
        ) {
          this.svgLayer.deviceMap.get(this.deviceID).doMouseDownCore();
        }
      })
      .on("mouseup", () => {
        if (G.mouseEventBtn() == 1 || G.mouseEventBtn() == 2)
          this.svgLayer.deviceMap.get(this.deviceID).doMouseUpCore();
      })
      .on("dblclick", () => {
        this.svgLayer.deviceMap.get(this.deviceID).doDoubleClick();
      });

    //Event.on('valuechange',(key:ATTR_ENUM,oldValue:string | number | null,newValue:string | number | null)=>{
    // });
  }

  /**
   * 绘制标题文字
   */
  drawTextGraphic() {
    //---2017.03.30隐藏显示Node名称
    this.textGraphic = this.svgLayer.graphicLayer_textLayer_coreTextLayer
      .append("text")
      .attr("x", this.x)
      .attr("y", this.y)
      .attr("text-anchor", "middle")
      .attr("dy", this.textDy)
      .text(this.title)
      //双击重命名
      .on("dblclick", () => {
        //G.openRenameDialog(__this);
      });
  }

  /**
   * 设置元件核心的选中框样式
   * @param dx  x轴偏移
   * @param dy  y轴偏移
   * @param width  宽度
   * @param height  高度
   */
  setAidGraphicArg(dx: number, dy: number, width: number, height: number) {
    this.aidGraphicArg = { dx: dx, dy: dy, width: width, height: height };
  }

  /**
   * 显示选中状态辅助虚线框图形
   */
  public showAidGraphic() {
    if (!this.aidGraphic)
      this.aidGraphic = this.svgLayer.graphicLayer_aidGraphicLayer
        .append("rect")
        .attr("transform", this.mainGraphic.attr("transform"))
        .attr("x", this.x + this.aidGraphicArg.dx)
        .attr("y", this.y + this.aidGraphicArg.dy)
        .attr("width", this.aidGraphicArg.width)
        .attr("height", this.aidGraphicArg.height)
        .attr("fill", "none")
        .attr("stroke", "blue")
        .attr("stroke-width", "1")
        .attr("stroke-dasharray", "4,4");
  }

  /**
   * 隐藏选中状态辅助虚线框
   */
  public hideAidGraphic() {
    if (this.aidGraphic) this.aidGraphic.remove();
    this.aidGraphic = null;
  }

  /**
   * 移动
   * @param dx  x轴移动量
   * @param dy  y轴移动量
   */
  public move(dx: number, dy: number) {
    this.movePure(dx, dy, this.rotate);
    this.svgLayer.deviceMap.get(this.deviceID).moveCoreLinks(dx, dy);
  }

  /**
   * 纯移动，不移动关联元素
   * @param dx  x轴移动量
   * @param dy  y轴移动量
   * @param rotate  移动后的旋转角度
   */
  public movePure(dx: number, dy: number, rotate?: number) {
    let newCoordinate = { x: this.x + dx, y: this.y + dy };
    rotate == null ? (rotate = this.rotate) : null;
    this.moveToPure(newCoordinate, rotate);
  }

  /**
   * 移动到指定位置，移动时调整关联元素位置
   * @param newCoordinate 目标坐标
   */
  public moveTo(newCoordinate: Point) {
    this.moveToPure(newCoordinate, this.rotate);
    this.svgLayer.deviceMap.get(this.deviceID).moveToCoreLinks(newCoordinate);
  }

  /**
   * 纯移动到指定位置，不移动关联元素
   * @param newCoordinate  目标坐标
   * @param rotate  移动后的旋转角度
   */
  public moveToPure(newCoordinate: Point, rotate: number) {
    this.x = newCoordinate.x;
    this.y = newCoordinate.y;
    this.rotate = rotate;
    this.mainGraphic
      .attr("x", this.x)
      .attr("y", this.y)
      .attr(
        "transform",
        "rotate(" + this.rotate + "," + this.x + "," + this.y + ")"
      );

    this.textGraphic.attr("x", this.x).attr("y", this.y);

    if (this.aidGraphic)
      this.aidGraphic
        .attr("transform", this.mainGraphic.attr("transform"))
        .attr("x", this.x + this.aidGraphicArg.dx)
        .attr("y", this.y + this.aidGraphicArg.dy);
  }

  /**
   * 移除、删除
   */
  public remove() {
    if (this.mainGraphic) this.mainGraphic.remove();
    this.mainGraphic = null;
    if (this.textGraphic) this.textGraphic.remove();
    this.textGraphic = null;
    this.hideAidGraphic();
    this.id = null;
  }
}
