import axios from '@/libs/api.request'
// 新建工程 
export const newProjectApi = (data) => {
  return axios.request({
    url: '/gwwlmxgl/newProject',
    data,
    method: 'post'
  })
}
//获取工程列表
export const getProjectListApi = () => {
  return axios.request({
    url: '/gwwlmxgl/getProject',
    method: 'post'
  })
}
// 属性表默认值总表对象
export const getattributelistApi = () => {
  return axios.request({
    url: '/gwwlmxgl/getattributelist',
    method: 'post'
  })
}
// 保存工程
export const saveprojectApi = ( issaveproaff,projectid,proaffremark,projectaffid,arclist,attrilist,elementlist,nodelist) => {
  let data = {
    issaveproaff:issaveproaff,
    projectid:projectid,
    remark:proaffremark,
    projectaffid:projectaffid,
    arclist:arclist,
    attrilist:attrilist,
    elementlist:elementlist,
    nodelist:nodelist
  }
  console.log(data)
  return axios.request({
    url: '/gwwlmxgl/saveproject',
    data,
    method: 'post'
  })
}
