  /**
   * 设置待绘元件的类型，如果传入null，则表示清空待绘元件参数
   * @param type 元件类型
   */
  setType2draw(type: DEVICE_TYPE | null) {
    if (type) {
      this.deviceManager.ready2draw.setType2draw(type);
      this.stateManager.isCapturingNode(true);
    } else {
      this.deviceManager.reNewReady2draw();
      this.stateManager.isCapturingNode(false);
    }
  }

  setType2drawByString(type: string) {
    let trueType = <any>DEVICE_TYPE[<any>type];
    this.deviceManager.ready2draw.setType2draw(trueType);
    this.stateManager.isCapturingNode(true);
  }

  /**
   * 获得当前用户待绘元件类型
   * @returns {string}
   */
  getType2draw(): DEVICE_TYPE {
    return this.deviceManager.ready2draw.getType2draw();
  }

  /**
   * 判断svglayer是否处于mousedown的状态
   * @returns {boolean}
   */
  private isMouseDownSvgLayer(): boolean {
    return this.stateManager.isMouseDown;
  }

