  /**
   * 注册document事件
   */
  private addDocumentEventListen() {
    this.addMouseEventListen();
    this.addKeyDownEventListen();
  }

  /**
   * 注册鼠标点击事件
   */
  private addMouseEventListen() {
    //let _this = this;
    d3.select(document).on("mouseup", () => {
      //如果是非svg绘图区操作引起的mouseup，则不进行处理
      if (!this.isMouseDownSvgLayer()) {
        return;
      }
      //如果存在待绘元素，则绘图,
      if (
        this.deviceManager.ready2draw.getType2draw() &&
        G.mouseEventBtn() == 1
      ) {
        //this.deviceManager.ready2draw.endPoint = this.svgLayerManager.currentSvgLayer.getMouseSvgCoordinate();
        this.deviceManager.ready2draw.setCurrentSvgLayer(
          this.stateManager.currentSvgLayer
        );
        //绘制元件,如果元件绘制成功，则对layer进行大小相关调整，用以产生匹配的滚动条
        if (this.deviceManager.drawReadyDevice()) {
          this.doAfterDrawReadyDevice();
        }
        //通知svgLayer鼠标已经弹起
        //this.stateManager.mouseUpReseting();
      }

      //移动Node并捕捉到另一个Node
      else if (
        this.stateManager.nodeSelected &&
        this.stateManager.nodeMovingCaptured &&
        G.mouseEventBtn() == 1
      ) {
        this.stateManager.nodeSelected.devicePosMap.forEach((pos, deviceID) => {
          let device = this.deviceManager.getDeviceByID(deviceID);
          device.replaceNode(this.stateManager.nodeMovingCaptured, pos);
        });
        this.stateManager.nodeMovingCaptured.select();
      }

      this.stateManager.mouseUpReseting();
    });
  }

  /**
   * 注册键盘事件
   */
  private addKeyDownEventListen() {
    let stateManager = this.stateManager;
    document.onkeydown = function(event) {
      let e = event || window.event || arguments.callee.caller.arguments[0];

      //全选
      if (e && e.ctrlKey && e.keyCode == 65) {
        // 按ctrl+a
        e.preventDefault();
        //获取当前画布上的所有元件并选中
        stateManager.currentSvgLayer.deviceMap.forEach(device => {
          device.select();
        });
      }

      //复制
      if (e && e.ctrlKey && e.keyCode == 67) {
        // 按ctrl+c
        if (
          stateManager.deviceSelectedArray &&
          stateManager.deviceSelectedArray.length
        )
          stateManager.copyDevices();
      }

      //粘贴
      if (e.ctrlKey && e.keyCode == 86) {
        // 按ctrl+v

        if (
          stateManager.deviceCopyArgsArray &&
          stateManager.deviceCopyArgsArray.length
        )
          stateManager.pasteDevices();
      }

      //剪切
      if (e.ctrlKey && e.keyCode == 88) {
        // 按ctrl+x
        if (
          stateManager.deviceSelectedArray &&
          stateManager.deviceSelectedArray.length
        ) {
          stateManager.cutDevices();
        }
      }

      //删除
      if (e.keyCode == 46) {
        // 按delete
        if (
          stateManager.deviceSelectedArray &&
          stateManager.deviceSelectedArray.length
        )
          stateManager.deleteDevices();
      }
      //取消选中待绘元件
      if (e.keyCode == 27) {
        // 按ESC
        //this.doElementSelectBtnUp();
      }
    };
  }

  /**
   * 将类库外部方法注册到绘图结束后条用的方法上,外部调用方法
   * @param userFunction_doAfterDrawReadyDevice   外部方法
   */
  register_doAfterDrawReadyDevice(
    userFunction_doAfterDrawReadyDevice: Function
  ) {
    this.userFunction_doAfterDrawReadyDevice = userFunction_doAfterDrawReadyDevice;
  }

  /**
   * 绘图结束后调用外部注册的方法
   */
  private doAfterDrawReadyDevice() {
    try {
      this.userFunction_doAfterDrawReadyDevice();
    } catch (e) {
      throw new Error(
        "doAfterDrawReadyDevice函数未注册，请调用pb.register_doAfterDrawReadyDevice进行函数注册"
      );
    }
  }

