右部工况列表
<template>
  <div>
    <el-tree
      :data="treedata"
      default-expand-all="true"
      :props="defaultProps"
      @node-contextmenu="rightClick"
      @node-click="handleNodeClick"
    ></el-tree>
  </div>
</template>

<script>
export default {
  data() {
    return {
      defaultProps: {
        children: "children",
        label: "label"
      },
    };
  },
  props: {
    //接受Project全局变量
    project: null,
    treedata:{type:Array},
  },
  methods: {
    // addArc(arc) {
    //   this.childrendata.push({ label: arc.arcName, id: arc.id });
    // },
    //获取工况信息
    handleNodeClick(data, Node) {
      if (Node.level == 2) {
        this.project.loadArc2DeviceByName(Node.label);
      }                      
    },
    rightClick(event,porp,Node) {
     this.$emit('rightClick',Node);
    },
    //删除工况

  }
};
</script>