<template>
  <div>
    <el-dialog title="属性编辑" :visible.sync="dialogVisible" width="50%" center>
      <el-form
        :model="arrt"
        ref="ruleForm"
        label-width="100px"
        size="mini"
        class="demo-ruleForm"
        :inline="true"
      >
        <el-col :span="12" v-for="attritem in arrt.attrslist" :key="attritem.id">
                <el-form-item :label="attritem.title+':'">
                <el-tooltip class="item" effect="dark" :disabled="attritem.remark?!disabled:disabled" :content="attritem.remark" placement="top">                 
                    <el-input
                      v-model="attritem.value"
                      v-if="!attritem.item&&!attritem. opendilaog&&attritem.show"
                      :prop="attritem.value"
                      style="width:150px"
                    ></el-input>
                  </el-tooltip>
                   <el-tooltip class="item" effect="dark" :disabled="attritem.remark?!disabled:disabled" :content="attritem.remark" placement="top">      
                  <el-select
                    v-model="attritem.value"
                    placeholder="请选择"
                    v-if="attritem.item&&!attritem. opendilaog&&attritem.show"
                    size="mini"
                    style="width:150px"
                  >
                    <el-option
                      v-for="item in attritem.item"
                      :key="item.value"
                      :label="item.text"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                  </el-tooltip>
                  <el-tooltip class="item" effect="dark" :disabled="attritem.remark?!disabled:disabled" :content="attritem.remark" placement="top">      
                  <el-button
                    size="small"
                    style="width:150px"
                    v-if="attritem. opendilaog&&attritem.show"
                    @click="editattr(attritem.id)"
                  >编辑</el-button>
                  </el-tooltip>
                  <span v-if="attritem.unit">({{attritem.unit}})</span>
                </el-form-item>
           
        </el-col>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm('ruleForm');">确 定</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </span>
    </el-dialog>
    <el-dialog title="高程地温曲线设置" :visible.sync="gcdwdialogVisible" width="50%" center>
      <gcdw-vue :pltableData="pltableData" :project="project" :isopendialog="isopendialog" :plid="plid"></gcdw-vue>
    </el-dialog>
  </div>
</template>
<script>
//高程地温曲线设置
import GcdwVue from "./gcdwqx";
export default {
  data() {
    return {
      dialogVisible: false,
      gcdwdialogVisible: false,
      attrs: null,
      arrt: {
        attrslist: []
      },
      //管段 信息，包括管段名称 管长
      pltableData: [],
      //判断是否重新打开 高程 地温 曲线的弹出框
      isopendialog: false,
      plid:null
    };
  },
  props: {
    project: null
  },
  components: {
    GcdwVue
  },
  methods: {
    //编辑高程地温曲线数据
    editattr(attrid) {
      this.gcdwdialogVisible = true;
      this.plid=this.attrs.id;
      if (this.isopendialog) {
        this.isopendialog = false;
      } else {
        this.isopendialog = true;
      }
    },
    opengkdialog(attrs) {
      this.attrs = attrs;
      this.dialogVisible = true;
      this.arrt.attrslist = attrs.getAttrs();
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.arrt.attrslist.forEach(item => {
            this.attrs.setAttrValue(item.id, item.value);
          });
          this.dialogVisible = false;
        }
      });
    }
  }
};
</script>
<style>
/* .dialog-class {
  height: 30%;
} */
</style>
