<template>
     <el-form ref="newsimulationform"  label-width="90px">
        <el-form-item  label-width="250px" label="埋地管道传热计算方法:" prop="simulationnameSim">
                <el-select v-model="select" placeholder="请选择" style="width: 330px">
                        <el-option label="2009 Method" value="1"></el-option>
                        <el-option label="2000 Method" value="2"></el-option>
                        <el-option label="1983 Method" value="3"></el-option>
                </el-select>
                <el-popover
                    placement="top-start"
                    title="埋地管道传热计算方法（Pipe burial method ）说明"
                    width="700"
                    trigger="click"
                    >
                    <el-row style="">
                  <P>Model to use for pipeline heat transfer calculations. The calculations use the burial configuration of the pipe (fully buried, partially buried, or fully exposed) and give different U-value results based on the model selected. The options, in decreasing order of accuracy, are: </P>
                    <ul>
                        <li>2009 Method (default)</li>
                        <li>2000 Method</li>
                        <li>1983 Method</li>
                    </ul>
                    <P>All options produce identical results for a fully exposed pipeline, but the results are different for a fully buried or partially buried pipe.</P>
                    </el-row>
                    <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                </el-popover>
        </el-form-item>
