                    trigger="click"
                    >
                    <el-row style="">
                        <P>Inside film coefficient (IFC) calculation model for heat loss calculations. </P>
                        <ul>
                            <li>Kreith combined Reynolds number model (default)</li>
                            <li>Kaminsky model </li>
                        </ul>
                    </el-row>
                    <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                </el-popover>
        </el-form-item>
