/**
 * 工况类
 * Created by ZJY on 2017/9/7.
 */
import {DeviceManager} from "./PB_DeviceManager";
import {copyMap} from "./PB_Tools";
import {Attribute} from "./PB_Attribute";
import ATTR_ENUM = Attribute.ATTR_ENUM;
import AIV_Map = Attribute.AttrIndex2ValueMap;


export class Arc {

    static ST_LAST_ID = 0;
    //工况ID
    private id: number;
    //工况名称
    private arcName: string;
    //工况描述
    private arcDescribe:string;
    //工况中保存的元件及其属性
    private device2AttrMap: Map<string, AIV_Map>;
    //创建时间
    private arcCreatedate:Date
    /**
     *
     * @param id  工况ID
     * @param arcName 工况名称
     * @param arcDescribe 工况描述
     * @param device2AttrMap 元件->属性表集合
     */
    constructor(id: number | null, arcName: string,arcDescribe:string, device2AttrMap: Map<string, AIV_Map>) {
        this.id = id == null ? Arc.ST_LAST_ID : id;
        Arc.ST_LAST_ID++;
        this.arcName = arcName == null ? "Arc_Default" : arcName;
        this.arcCreatedate=new Date();
        if (!this.device2AttrMap)
            this.setDevice2AttrMap(device2AttrMap);
    }

    getId(): number {
        return this.id;
    }

    getName(): string {
        return this.arcName;
    }

    setDevice2AttrMap(device2AttrMap: Map<string, AIV_Map>) {
        this.device2AttrMap = new Map<string, AIV_Map>();
        device2AttrMap.forEach((index2ValueMap, deviceId) => {
            //map浅拷贝，防止引用传递
            this.device2AttrMap.set(deviceId, copyMap(index2ValueMap));
        });
    }

    /**
     * 向工况中增加新的元件及属性或修改现有元件的属性
     * @param deviceId  元件ID
     * @param index2ValueMap  元件属性
     */
    addOrModifyDeviceAndAttributeToArc(deviceId: string, index2ValueMap: AIV_Map): void {
        if (!this.device2AttrMap)
            this.device2AttrMap = new Map<string, AIV_Map>();
        //map浅拷贝，防止引用传递
        this.device2AttrMap.set(deviceId, copyMap(index2ValueMap));
    }

    /**
     * 从当前工况中移除元件及其属性
     * @param deviceId 元件ID
     */
    removeDeviceFromArc(deviceId: string): void {
        this.device2AttrMap.delete(deviceId);
    }


    /**
     * 将此工况加载到当前的设备元件上
     */
    loadArcToDevice(): void {
        if (this.device2AttrMap) {
            let DM = DeviceManager.getInstance();
            this.device2AttrMap.forEach((index2ValueMap: AIV_Map, deviceId: string) => {
                let device = DM.getDeviceByID(deviceId);
                device.set_AIV_Map(index2ValueMap);
            });
        }
    }

    /**
     * 修改当前工况中元件的属性值
     * @param deviceId  元件ID
     * @param key       元件属性Key
     * @param newValue  元件属性值
     */
    setCurArcByDeviceIdAndAttrKey(deviceId: string, key: ATTR_ENUM, newValue: string | number | null): void {
        this.device2AttrMap.get(deviceId).set(key, newValue);
    }

    public getJsonObject():{}{
        return {
            id:this.id,
            name:this.arcName,
            arcDescribe:this.arcDescribe==null?this.arcName:this.arcDescribe,
            arcCreatedate:this.arcCreatedate,
            device:this.getDeviceAndAttrJsonObjectArr()
        }
    }

    private getDeviceAndAttrJsonObjectArr():[any]{
        let arr:any = [];

        this.device2AttrMap.forEach((attr, deviceId) => {
            arr.push({
                id:deviceId,
                attr:this.getattrJsonArr(attr)
            })
        });

        return arr;
    }

    private  getattrJsonArr(attr:AIV_Map):[any]{
        let arr:any = [];
        attr.forEach((value, key) => {
            arr.push({
                k:key,
                v:value
            });
        });

        return arr;
    }

}
