<script>
export default {
  data() {},
  props: {
    //接受Project全局变量
    project: null,
    ishaveproject:{
      type:Boolean
    }
  },
  watch:{
   ishaveproject(val)
   {
    this.updatestyle(val)
   }
  },
  created() {},
  methods: {
    //创建完工程改变样式
    updatestyle(val)
    {
      if(val)
      {
       let classAtr = document.getElementById("muneDiv").getAttribute("class");
       let newClass = classAtr.replace("gray","");
       document.getElementById("muneDiv").setAttribute("class",newClass);
       //绘制元件成功后 回调
        let _this = this;
        this.project.register_doAfterDrawReadyDevice(function() {
          _this.resettingDeviceBtns();
        });
      }
    },
    //绘制元件成功后 回调
    resettingDeviceBtns() {
      let elementList = document.querySelectorAll("#tab_1");
      let divel = elementList[0];
      let ulel = divel.children;
      //let _this = this;
      for (let i = 0; i < ulel.length; i++) {
        ulel[i].setAttribute("style", "");
      }
      this.project.setType2draw(null);
    }
  },
  mounted() {
    let elementList = document.querySelectorAll("#tab_1");
    let divel = elementList[0];
    let ulel = divel.children;
    let _this = this;
    for (let i = 0; i < ulel.length; i++) {
      //选择某个元件进行绘制
      ulel[i].children[0].children[0].onmousedown = function() {
        if(_this.project==null)
        {
          _this.$message({
              message: "系统处于关闭状态！请创建或打开工程在进行绘制！",
              type: 'warning',
              duration:8000
            });
            return false
        }
        _this.resettingDeviceBtns();
        if (ulel[i].getAttribute("type") == "CZ") {
          _this.resettingDeviceBtns();
        } else {
          _this.$emit("selectelement", ulel[i].getAttribute("type"));
          ulel[i].setAttribute("style", "background-color:#5FA2DD;");
        }
      };
    }
    
  }
};
</script>

<style>
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
}

ul {
  border: 1px solid #fff;
}
ul:hover {
  border: 1px solid #000000;
}

.btn_aa {
  display: block;
  background: none;
  color: black;
  border: none;
  padding: 5px;
}

.gray {
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  -o-filter: grayscale(100%);

  filter: grayscale(100%);

  filter: gray;
}
.draw-element {
  float: left;
  margin-left: 5px;
  margin-top: 5px;
}
.draw-element_close {
  float: left;
  margin-left: 5px;
  margin-top: 5px;
  filter: grayscale(100%);
}
</style>

 