高程地温曲线配置
<template>
  <div>
    <!-- <el-row style="margin-bottom:10px">
      <span style="font-weight:900">站场/阀室:</span>
      <el-select v-model="value8" filterable placeholder="请选择" style=" width:80%" size="mini">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
    </el-row>-->
    <el-row>
      <el-table
        ref="singleTable"
        :data="pltableData"
        highlight-current-row
        style="width: 100%"
        height="200px"
        size="mini"
        @current-change="handleCurrentChange"
        border
      >
        <el-table-column align="center" type="index" width="50" label="序号"></el-table-column>
        <el-table-column align="center" property="plname" label="管段"></el-table-column>
        <el-table-column align="center" property="pllength" label="管长"></el-table-column>
      </el-table>
    </el-row>
    <el-row>
      <el-col :span="12">
        <el-table :data="gctableData" stripe style="width: 100%;" border height="300px" size="mini">
          <el-table-column align="center" type="index" width="50" label="序号"></el-table-column>
          <el-table-column prop="text" column-key="text" align="center" label="里程（KM）">
            <template slot-scope="scope">
              <input
                type="text"
                style="border:none;width: 100%;height:30px;text-align: center;"
                :value="scope.row.text"
              >
            </template>
          </el-table-column>
          <el-table-column prop="value" align="center" label="高程（m）">
            <template slot-scope="scope">
              <input
                type="text"
                style="border:none;width: 100%;height:30px;text-align: center;"
                :value="scope.row.value"
              >
            </template>
          </el-table-column>
        </el-table>
      </el-col>
      <el-col :span="12">
        <el-table :data="dwtableData" stripe style="width: 100%;" border height="300px" size="mini">
          <el-table-column align="center" type="index" width="50" label="序号"></el-table-column>
          <el-table-column prop="text" column-key="text" align="center" label="里程（KM）">
            <template slot-scope="scope">
              <input
                type="text"
                style="border:none;width: 100%;height:30px;text-align: center;"
                :value="scope.row.text"
              >
            </template>
          </el-table-column>
          <el-table-column prop="value" align="center" label="地温（℃）">
            <template slot-scope="scope">
              <input
                type="text"
                style="border:none;width: 100%;height:30px;text-align: center;"
                :value="scope.row.value"
              >
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //   options: [
      //     {
      //       value: "选项1",
      //       label: "黄金糕"
      //     },
      //     {
      //       value: "选项2",
      //       label: "双皮奶"
      //     },
      //     {
      //       value: "选项3",
      //       label: "蚵仔煎"
      //     },
      //     {
      //       value: "选项4",
      //       label: "龙须面"
      //     },
      //     {
      //       value: "选项5",
      //       label: "北京烤鸭"
      //     }
      //   ],
      //   value8: "",
      pltableData: [],
      gctableData: [
        { value: 0, text: 1.2 },
        { value: 100, text: 1.2 },
        { value: 200, text: 1.2 },
        { value: 300, text: 1.2 },
        { value: 400, text: 1.2 },
        { value: 500, text: 1.2 }
      ],
      dwtableData: [
        { value: 0, text: 35 },
        { value: 100, text: 34 },
        { value: 200, text: 35 },
        { value: 300, text: 35 },
        { value: 400, text: 36 },
        { value: 500, text: 36 }
      ]
    };
    elementall: [];
  },
  props: {
    project: null,
    isopendialog: true,
    plid: null
  },
  watch: {
    isopendialog: {
      deep: false,
      handler() {
        this.reload();
      }
    }
  },
  mounted() {
    this.reload();
  },
  methods: {
    reload() {
      this.elementall = this.project.getElementInfo();
      let pltablelist = [];
      let dataindex = 0;
      let dataindex1 = 0;
      this.elementall.forEach(item => {
        if (item.deviceType === 3) {
          //获取管长
          let pllength = item.attrIndex2ValueMap.get(7);
          //获取编号 或 标题
          let plname = item.attrIndex2ValueMap.get(0);
          if (item.id === this.plid) {
            dataindex1 = dataindex;
          }

          pltablelist.push({
            plid: item.id,
            plname: plname,
            pllength: pllength
          });
          dataindex++;
        }
      });
      this.pltableData = pltablelist;
      this.$refs.singleTable.setCurrentRow(this.pltableData[dataindex1]);
    },
    //单选触发器
    handleCurrentChange(currentRow, oldCurrentRow) {
      this.gcdwTableData();
    },
    //给高程地温曲线表格赋值
    gcdwTableData() {
      console.log(this.elementall.get(this.plid).attrIndex2ValueMap);
    }
  }
};
</script>