左部元件按钮
<template>
  <el-collapse v-model="activeNames" @change="handleChange">
    <el-collapse-item name="1" style="background-color: #d3dce6;">
      <template slot="title">
        <div style="margin-left:20px;font-weight:900">基本元件</div>
      </template>
      <maps-tool @selectelement="selectelement" :project="project" :ishaveproject="ishaveproject"></maps-tool>
    </el-collapse-item>
    <el-collapse-item name="2">
      <template slot="title">
        <div style="margin-left:20px;font-weight:900">特殊元件</div>
      </template>
      <div>控制反馈：通过界面样式和交互动效让用户可以清晰的感知自己的操作；</div>
      <div>页面反馈：操作后，通过页面元素的变化清晰地展现当前状态。</div>
    </el-collapse-item>
  </el-collapse>
</template>
<script>
import MapsTool from "./mapstool";
export default {
  data() {
    return {
      activeNames: ["1"]
    };
  },
  props: {
    project: null,
    ishaveproject:{
      type:Boolean
    }
  },
  components: {
    MapsTool
  },
  created() {},
  methods: {
    selectelement(elementtype) {
      this.$emit("selectelement", elementtype);
    },
    handleChange(val) {
      console.log(val);
    }
  },
  mounted() {}
};
</script>
