首页布局
<template>
  <div>
    <el-container>
      <!-- 顶部菜单栏 -->
      <el-header class="el-header1">
        <top-menu :project="project" @projectopening="projectopening" @saveprojectmsg="saveprojectmsg"></top-menu>
      </el-header>
      <el-container>
        <!-- 左部元件列表 -->
        <el-aside width="180px" v-if="show">
          <el-card class="box-card" body-style="height: calc(100vh - 105px);">
            <left-menu @selectelement="selectelement" :project="project" :ishaveproject="ishaveproject"></left-menu>
          </el-card>
        </el-aside>

        <el-main class="el-main1">
          <!-- 标签切换（切换画布） -->
          <el-card class="box-card" body-style="padding:0px">
            <el-row class="el-header2">
              <tag-menu :taglist="taglist" @selecttag="selectSvgdiv"></tag-menu>
            </el-row>
          </el-card>
          <el-container>
            <!-- 标签切换（主绘画区） -->
            <el-main class="el-main2">
              <el-card
                class="box-card"
                body-style="padding:0px;height: calc(100vh - 105px);background-color: #e9eef3;"
              >
                <div ref="svglayer"></div>
              </el-card>
            </el-main>
          </el-container>
        </el-main>
        <!-- 右部工况列表 -->
        <el-aside width="180px">
          <el-card class="box-card" body-style="height: calc(100vh - 105px);">
            <right-menu :project="project" ref="rightmenu" @rightClick="rightClick" :ishaveproject="ishaveproject" :treedata="gktreedata"></right-menu>
          </el-card>
        </el-aside>
      </el-container>
    </el-container>
     <!-- 右键菜单 -->
    <right-key-menu ref="rightkeymenu" :contextitem="contextmenu"></right-key-menu>
    <!-- 添加工况 -->
    <add-gk ref="addgk" :project="project" @treeAddArc="treeAddArc"></add-gk>
    <!-- 属性编辑 -->
    <attrs-show ref="attrsshow" :project="project"></attrs-show>
  </div>
</template>
<script>
import TopMenu from "./topmenu";
import LeftMenu from "./leftmenu";
import TagMenu from "./tagmenu";
import RightMenu from "./rightmenu";

import { Project } from "@/lib/PipeBuilderLib";
import {
  popArcMenu,
  regeister_PopContextMenu,
  register_ZeroNodeDeviceDoAfterDoubleClick,
  register_ZeroNodeDeviceDoAfterDelete,
  register_Pop_newArcDlg,
  register_PopAttributeDilaog,
  register_DelArcFromTree,
  register_CloseMenu,
  register_SetAttribute
} from "@/lib/PipeBuilderLib/index";

//添加工况弹出框
import AddGk from "../components/addarc";
//属性编辑
import AttrsShow from "../components/attrsshow";
//右键菜单
import RightKeyMenu from "../components/rightkeymenu";
import { constants } from "fs";
import {
  getattributelistApi,
  saveprojectApi,
  openprojectbyprojectidApi
} from '@/api/gwwlmxgl'
export default {
  data() {
    return {
      //工况列表
      gktreedata:[],
      show: true,
      project: null,
      //Index: null,
      dialogVisible: false,
      contextmenu: [],
      taglist: [],
      //是否存在工程
      ishaveproject:false
    };
  },
  components: {
    TopMenu,
    LeftMenu,
    TagMenu,
    RightMenu,
    AddGk,
    AttrsShow,
    RightKeyMenu
  },
  created() {
        let _this = this;
    //右键菜单
    regeister_PopContextMenu((event, items, width = 200) => {
     
       //_this.contextmenu = items;
      if(!event){
      }
      else
      {
        this.$refs.rightkeymenu.oncontextmenu(event,items)
      }
    });
    //双击站场 阀室
    register_ZeroNodeDeviceDoAfterDoubleClick(newSvgLayer => {
      //创建新Tab
      if (
        _this.taglist.filter(d => {
          if (d.id === newSvgLayer.id) {
            return true;
          }
        }).length < 1
      ) {
        _this.taglist.push({
          id: newSvgLayer.id,
          title: newSvgLayer.title,
          isselect: true,
          isshow: true
        });
        this.$refs.svglayer.appendChild(newSvgLayer.getDom());
      }
      _this.selectSvgdiv(newSvgLayer.id);
    });
    //删除站场 或 阀室 已经 标签和画布
    register_ZeroNodeDeviceDoAfterDelete(tabId => {
      _this.taglist = _this.taglist.filter(d => {
        if (d.id !== tabId) {
          return true;
        }
      });
      let childNodes = this.$refs.svglayer.childNodes;
      for (let index = 0; index < childNodes.length; index++) {
        if (childNodes[index].id === "pb_" + tabId + "_div") {
          this.$refs.svglayer.removeChild(childNodes[index]);
          childNodes[index - 1].style.display = "block";
        }
      }
    });
    //新建工况
    register_Pop_newArcDlg(this.opengkdialog);
    //删除工况
    register_DelArcFromTree(this.deletegk);
    register_CloseMenu(this.closeMenu)
    //属性弹出框
    register_PopAttributeDilaog(attrs => {
      this.$refs.attrsshow.opengkdialog(attrs);
    });
     this.getattributelist()
  },
  mounted() {
   
  },
  methods: {
    reload()
    {
      this.gktreedata=[];
      this.contextmenu=[];
      this.taglist=[];
      this.ishaveproject=[]
    },
    //获取默认属性列表
    getattributelist(){
      const qjloading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
      getattributelistApi().then(res=>{
         register_SetAttribute(res.data.data);
         qjloading.close();
      }).catch(err => {
        qjloading.close();
            this.$message({
              message: '服务器错误:'+err,
              type: 'error',
              duration:8000
      });
      })
    },
    //关闭右键菜单
    closeMenu()
    {
      this.$refs.rightkeymenu.onclose()
    },
    //保存工程提示信息
    saveprojectmsg(projectid,projectidtype)
    {
      if(this.ishaveproject)
        this.$confirm('是否将工程保存到备份版本?', '提示', {
          distinguishCancelAndClose: true,
          showInput:true,
          inputPlaceholder:"请填写保存的备注信息",
          //inputValue:"dddd",
          confirmButtonText: '是',
          cancelButtonText: '否',
          center:"true",
          type: 'warning'
        }).then(({proaffremark}) => {
        this.saveprojectfun(1,projectid,proaffremark,projectidtype)  
        }).catch((action) => {
           //onsole.log(action)
          if(action === 'cancel')
              this.saveprojectfun(0,projectid,"",projectidtype)   
        });
      else
        this.$message({
          message: '没有要保存的工程信息！',
          type: 'warning'
        });
    },
  
    /**保存工程
     * issaveproaff 是否保存为备份
     * projectid 工程表ID或工程备份表ID
     * proaffremark 保存时备注
     * projectidtype 1:工程表ID，2：工程备份表ID
     */
    saveprojectfun(issaveproaff,projectid,proaffremark,projectidtype)
    {
          const qjloading = this.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
          //日期格式化
          const formatTime = date => {
            const year = date.getFullYear()
            const month = date.getMonth() + 1
            const day = date.getDate()
            const hour = date.getHours()
            const minute = date.getMinutes()
            const second = date.getSeconds()

            return [year, month, day].map(formatNumber).join('/') + ' ' + [hour, minute, second].map(formatNumber).join(':')
          }
          const formatNumber = n => {
            n = n.toString()
            return n[1] ? n : '0' + n
          }
          let prpjectList=this.project.getJsonObject();
          //console.log(JSON.stringify(prpjectList))
          //工况集合
          let arclist=[];
          //属性集合
          let attrilist=[];
          //元件集合
          let elementlist=[];
          //Node节点集合
          let nodelist=[];
          //画布集合
          //let layerlist=[]
           console.log(prpjectList)
          prpjectList.arcList.forEach(element => {
            //console.log(formatTime(element.arcCreatedate))
            arclist.push({
              workidWork:element.id,
              worktypeWork:0,
              worknameWork:element.name,
              remarkProaff:element.arcDescribe,
              projectidProaff:prpjectList.affid,
              createdateWork:formatTime(element.arcCreatedate)
            })
            element.device.forEach(deviceelement => {
              deviceelement.attr.forEach(attr => {
                attrilist.push({
                  workidWork:element.id,
                  propertyidPro:attr.k,
                  provalueProval:attr.v,
                  projectidProaff:prpjectList.affid,
                  elementrealnameEle:deviceelement.id})
              });
            });
          });
          prpjectList.deviceArr.forEach(element => {
            elementlist.push({
              elementrealnameEle:element.id,
              nodestarEle:element.p1,
              nodeendEle:element.p2,
              layeridEle:element.layer,
              elementtypeEle:element.type,
              rotateEle:element.rotate,
              starxEle:element.cp.x,
              staryEle:element.cp.y,
              projectidProaff:prpjectList.affid
            })
          })
          prpjectList.nodeList.forEach(element => {
            nodelist.push({
              layeridEle:element.pt.svgLayerid,
              nodenumNdl:element.id,
              nodexNdl:element.pt.x,
              nodeyNdl:element.pt.y,
              projectidProaff:prpjectList.affid
            })
          })
          
          saveprojectApi(issaveproaff,prpjectList.id,proaffremark,prpjectList.affid,arclist,attrilist,elementlist,nodelist).then(res=>{
          if(res.data.status===200)
          {
              qjloading.close()
              if(projectid)
              {
                this.projectopenfun(projectid,projectidtype)
              }
              this.project.affid=res.data.data
              this.$message({
                message: '工程保存成功！',
                type: 'success',
                duration:8000
              });
             }
            else
            {
              this.$message({
                message: '创建失败:'+res.data.msg,
                type: 'error',
                duration:8000
              });
            }
          }).catch(err => {
             qjloading.close()
             this.openprojectloading=false;
            this.$message({
              message: '服务器错误:'+err,
              type: 'error',
              duration:8000
            });
          })
    },
    //打开工程时
    projectopening(projectid,projectidtype)
    {
      if(this.ishaveproject)
      {
          this.$confirm('还有在运行的工程信息，是否先保存再打开工程?', '提示', {
            confirmButtonText: '是',
            cancelButtonText: '否',
            type: 'warning'
          }).then(() => {
           this.saveprojectmsg(projectid,projectidtype)  
        }).catch((action) => {
          if(action === 'cancel')
          this.projectopenfun(projectid,projectidtype)   
        });
        
      }
      else
      {
        this.projectopenfun(projectid,projectidtype)
      }   
    },
    projectopenfun(projectid,projectidtype)
    {
      
          const qjloading = this.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
         openprojectbyprojectidApi(projectid,projectidtype).then(res=>{
           console.log(res.data.data)
          this.project = new Project("","");
          let projectlist= res.data.data//
         
          this.ishaveproject=true;
          this.project.loadToProject(projectlist)
          let defaultSvglayer = this.project.svgLayerManager.getDefaultSvgLayer();
          this.project.stateManager.switchCurrentSvgLayerById("defaultLayer");
          console.log(defaultSvglayer)
          this.taglist.push({
            //画布编号
            id: defaultSvglayer.id,
            //标签名
            title: defaultSvglayer.title,
            //当前是否已选择该标签
            isselect: true,
            //是否显示该标签
            isshow: true
          });
          this.$refs.svglayer.appendChild(defaultSvglayer.getDom());
          let children=[]
          console.log(this.project)
          this.project.getAllArcInfo().forEach(element => {
            children.push({label: element.arcName, id: element.id})
          });
          this.gktreedata.push({label:projectlist.name,children:children})
          qjloading.close()
      })
      .catch(err => {
             qjloading.close()
             this.openprojectloading=false;
            this.$message({
              message: '服务器错误:'+err,
              type: 'error',
              duration:8000
          });
      })
      
    },
    //新建工程后
    projectload(id,name,affid)
    {
      console.log(id)
      this.project = new Project(id,name,affid);
      this.ishaveproject=true;
      this.project.setType2draw(null);
      let defaultSvglayer = this.project.svgLayerManager.getDefaultSvgLayer();
      //添加工况信息
      let children=[]
     
      this.project.getAllArcInfo().forEach(element => {
        children.push({label: element.arcName, id: element.id})
      });
      this.gktreedata.push({label:name,children:children})
      this.taglist.push({
        //画布编号
        id: defaultSvglayer.id,
        //标签名
        title: defaultSvglayer.title,
        //当前是否已选择该标签
        isselect: true,
        //是否显示该标签
        isshow: true
      });
      this.$refs.svglayer.appendChild(defaultSvglayer.getDom());
    },
    //打开新建工程对话框
    opengkdialog() {
      this.$refs.addgk.opengkdialog();
    },
    //创建完工况 给树形添加数据
    treeAddArc(arc) {
       this.gktreedata[0].children.push({ label: arc.arcName, id: arc.id });
      //this.$refs.rightmenu.addArc(arc);
    },
    //工况右键弹出框
    rightClick(Node) {
        popArcMenu(Node.data.id,Node.data);
    },
    //删除工况
    deletegk(node)
    {
      this.gktreedata[0].children=this.gktreedata[0].children.filter(item=>item.id!=node.id)
      this.project.removeArcByArcName(node.label)
    },
    //隐藏 或 显示绘制面板
    selectSvgdiv(divid) {
      let childNodes = this.$refs.svglayer.childNodes;
      for (let index = 0; index < childNodes.length; index++) {
        if (childNodes[index].id !== "pb_" + divid + "_div") {
          childNodes[index].style.display = "none";
        } else {
          //切为当前画布
          this.project.stateManager.switchCurrentSvgLayerById(divid);
          childNodes[index].style.display = "block";
        }
      }
      for (let index = 0; index < this.taglist.length; index++) {
        if (this.taglist[index].id === divid) {
          //如果存在关闭标签的情况 则进行显示
          this.taglist[index].isshow = true;
          //选中当前画布的标签
          this.taglist[index].isselect = true;
        } else {
          this.taglist[index].isselect = false;
        }
      }
    },
    //选中元件 绘制图形
    selectelement(elementtype) {
      event.stopImmediatePropagation();
      event.preventDefault();
      this.project.setType2drawByString(elementtype);
    }
  }
};
</script>
<style>
.popperclass {
  min-width: 0px;
}
.el-header1 {
  line-height: 60px;
  padding: 0px;
}
.el-header2 {
  line-height: 40px;
  padding: 0px;
  text-align: left;
}
.el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: center;
}

.el-main1 {
  /* background-color: #e9eef3; */
  color: #333;
  text-align: center;
  padding: 0px;
  line-height: calc(100vh - 130px);
}
.el-main2 {
  /* background-color: #80ffff; */
  color: #333;
  text-align: center;
  padding: 0px;
}

body > .el-container {
  margin-bottom: 40px;
}
</style>