<template>
      <el-form ref="newsimulationform" :model="newsimulationmodel" :rules="rules" label-width="90px">
          <el-collapse v-model="activeNames" accordion>
                <el-collapse-item title="管道分割数据(Pipe segmentation data)" name="1">
                     <el-form-item label="短管输出节点最大长度:" label-width="350px" prop="">
                              <el-input  style="width: 260px" placeholder=""></el-input>
                               <span style="padding-left:10px;padding-right:10px">m</span>
                              <el-popover
                                placement="top-start"
                                title="短管输出节点最大长度（Max. report interval length）说明"
                                width="700"
                                trigger="click"
                                >
                                <el-row >
                                    Optionally specify the maximum report interval length to generate profile results at shorter distances along the pipe segment.                                
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                              </el-popover>
                    </el-form-item>
                    <el-form-item label="输出计算管段参数:" label-width="350px" prop="">
                              <el-checkbox></el-checkbox>
                             <span style="padding-left:10px;padding-right:10px">m/s</span>
                             <el-popover
                                placement="top-start"
                                title="输出计算管段参数（Print computation segment results）"
                                width="700"
                                trigger="click"
                                >
                                <el-row>
                                    Select this check box to report the results for each computation segment. This report may include very short pipe segments if required for the solution to converge or the option to use additional short segments across nodes, if selected.
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                             </el-popover>
                    </el-form-item>
                     <el-form-item label="计算管段输出设置:" label-width="350px" prop="">
                         <el-input  style="width: 260px" placeholder="" ></el-input>
                             <span style="padding-left:10px;padding-right:10px">kpa a</span>
                             <el-popover
                                placement="top-start"
                                title="计算管段输出设置(Computation segments per report interval）"
                                width="700"
                                trigger="click"
                                >
                                <el-row>
                                    You may specify the number of computational segments the engine uses per report interval. This action is similar to specifying the Max. computation segment length; however, the specification is relative to the report interval (either automatically set by the engine or user specified) rather than explicit. This option may be helpful when analyzing results associated with specific pipe segments that are reported.
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                             </el-popover>
                    </el-form-item>
                     <el-form-item label="最大计算管段长度:" label-width="350px" prop="">
                         <el-input  style="width: 260px" placeholder="" ></el-input>
                             <span style="padding-left:10px;padding-right:10px">m</span>
                             <el-popover
                                placement="top-start"
                                title="最大计算管段长度(Max. computation segment length ）"
                                width="700"
                                trigger="click"
                                >
                                <el-row>
                                    <p>Initial maximum segment length to be used by the program. Regardless of pipe length, data is calculated for sections of the given length. For example, if you specify 100ft, data is calculated for 10 segments of a 1000-foot-long pipe, or for 200 segments of a 20,000-foot-long pipe. </p>                              
                                    <p>To obtain a converged solution, PIPESIM may further subdivide the segment. </p>
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                             </el-popover>
                    </el-form-item>
                    <el-form-item label="在管道起终点添加短管:" label-width="350px" prop="">
                         <el-input  style="width: 260px" placeholder="" ></el-input>
                             <span style="padding-left:10px;padding-right:10px">kpa a</span>
                             <el-popover
                                placement="top-start"
                                title="在管道起终点添加短管(Additional short segments across nodes）"
                                width="700"
                                trigger="click"
                                >
                                <el-row>
                                    <p>Adds short (one foot) segments to the start and end of each pipe section. This feature ensures the reported fluid properties and flowrates are calculated at an almost identical temperature and pressure to that reported at the node. (In fact, the fluid properties are calculated at segment average pressure and temperature.) </p>                              
                                    <p>Enabling this parameter minimizes the discrepancies caused by this mismatch; however, this does effect run time. To disable this feature, clear the check box.</p>
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                             </el-popover>
                    </el-form-item>
                </el-collapse-item>
                <el-collapse-item title="闪蒸方法(Compositional flashing options）" name="2">
                        <el-form-item label="插值:" label-width="350px" prop="">
                                <el-select v-model="select" placeholder="请选择" style="width: 330px">
                                    <el-option label="Interpolate" value="2"></el-option>
                                    <el-option label="Hybrid" value="3"></el-option>
                                    <el-option label="Rigorous" value="2"></el-option>
                                </el-select>
                                <el-popover                                
                                    placement="top-start"
                                    title="插值(Interpolate）说明"
                                    width="700"
                                    trigger="click"
                                    >
                                    <el-row style="">
                                        <p>Controls the Temperature-Energy Balance. These values are used to maintain the temperature/enthalpy/entropy balance of the fluid.</p>
                                        <h3>Interpolate</h3>
                                        <p>This option uses interpolation between physical properties determined by in a predefined grid of temperature and pressure points. </p>
                                        <h3>Hybrid</h3>
                                        <p>This option is a compromise between speed and accuracy, which assumes that properties will change more rapidly when close to a phase boundary. Interpolation is performed whenever the grid points comprising a rectangle all show the presence of the same phases. For example, if all four (4) points in the rectangle have some oil, some gas, and no water, then you can assume the rectangle lies entirely within the 2-phase region of the hydrocarbon phase envelope, so interpolation is appropriate. If however one, two, or three of the points have no oil, then clearly the hydrocarbon dew point line crosses the rectangle, so a rigorous flash is required. </p>
                                        <h3>Rigorous</h3>
                                        <p></p>
                                    </el-row>
                                    <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                                </el-popover>
                        </el-form-item>
                        <el-form-item label="导热系数（Soil conductivity）:" label-width="350px" prop="">
                            <el-input  style="width: 260px" placeholder="" ></el-input>
                            <span style="padding-left:10px;padding-right:10px"> W/(m.K)</span>
                                <el-popover
                                    placement="top-start"
                                    title="导热系数（Soil conductivity）说明"
                                    width="700"
                                    trigger="click"
                                    >
                                    <el-row>
                                        Representative soil conductivity for each soil type you chose. The property may be specified by setting the Soil type to User defined.                                </el-row>
                                    <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                                </el-popover>
                        </el-form-item>
           </el-collapse-item>
    </el-collapse>
    
    </el-form> 
</template>
<script>
export default {
      data(){
        return{
            rules: {
                simulationnameSim: [
                  { required: true, message: '请填写仿真模拟方案名称', trigger: 'blur' },
                  { min: 3, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                ],
            },
            flowcorrelationmodel:{
                source:{
                    value:"",
                    remark:""
                }
            },
            select:""
        }
    },
    props:{
        // newsimulationmodel:{
        //     type:Array
        // }
    }
}
</script>