<template>
      <el-form ref="newsimulationform" :model="newsimulationmodel" :rules="rules" label-width="90px">
          <el-collapse v-model="activeNames" accordion>
                <el-collapse-item title="垂直多相源(Vertical multiphase source)" name="1">
                     <el-form-item label="源（Source）:" label-width="250px" prop="">
                        <!-- <el-input v-model="newsimulationmodel.simulationnameSim"  style="width: 260px" placeholder="请填写仿真模拟方案名称"> -->
                            <el-select v-model="select" placeholder="请选择" style="width: 330px">
                                <el-option label="Baker Jardine" value="1"></el-option>
                                <el-option label="Neotec" value="2"></el-option>
                                <el-option label="OLGAS" value="3"></el-option>
                                <el-option label="TUFFP Unified" value="3"></el-option>
                                <el-option label="Tulsa (Legacy 1989)" value="3"></el-option>
                            </el-select>
                              <el-popover
                                placement="top-start"
                                title="源（Source）说明"
                                width="700"
                                trigger="click"
                                >
                                <el-row style="height:300px;overflow:auto;">
                                    Defines the global, vertical, or horizontal source of the correlation. 
                                    <h3>Baker Jardine</h3>
                                    Developed by Schlumberger (originally Baker Jardine) and tested extensively. This value is the default source selection. 
                                    <h3>Neotec</h3>
                                    The Neotec flow correlations were developed by a company called Neotec based in Calgary. Neotec was formed in 1972 by Gary Gregory and Khalid Aziz, professors at the University of Calgary who specialized in Multiphase Flow research. Neotec developed several software applications used in the oil and gas industry, including WELLFLO, PIPEFLO and FORGAS. In 2010, Neotec was acquired by SPT Group and became part of Schlumberger in 2012 when Schlumberger acquired SPT Group. 
                                    <h3>OLGAS</h3>
                                    Based in large part on data from the SINTEF multiphase flow laboratory near Trondheim, Norway. The OLGA-S mechanistic models are applicable for all inclination angles, pipe diameters and fluid properties. The 2-phase model considers gas-liquid flow. The 3-phase model considers gas-oil-water flow. 
                                    <h3>TUFFP Unified</h3>
                                    Developed by the Tulsa University Fluid Flow Projects (TUFFP) research consortium. The models are applicable for all inclination angles, pipe diameters and fluid properties. The 2-phase model considers gas-liquid flow. The 3-phase model considers gas-oil-water pipe flow. 
                                    <h3>LedaFlow PM</h3>
                                    The LedaFlow Point Model is the steady-state version of the transient model developed by SINTEF in collaboration with Total and ConocoPhillips and commercialized by Kongsberg. It is applicable for all inclination angles, pipe diameters and fluid properties. The 2-phase model considers gas-liquid flow. The 3-phase model considers gas-oil-water flow. 
                                    <h3>Tulsa (Legacy 1989)</h3>
                                    Developed by the University of Tulsa, USA, and last modified by Professor Jim Brill, February 1989. The code is usually of academic quality and may return errors. No modifications have been made to accommodate extreme conditions or ensure mathematical stability. These models are included only for the purpose of validating calculations against publications and other simulators using the same code. Not recommended for general use. 
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                              </el-popover>
                    </el-form-item>
                    <el-form-item label="关联关系（Correlation）:" label-width="250px" prop="">
                         <el-select v-model="select" placeholder="请选择" style="width: 330px">
                                <el-option label="Ansari" value="1"></el-option>
                                <el-option label="Beggs & Brill Original" value="2"></el-option>
                                <el-option label="Beggs & Brill Revised" value="3"></el-option>
                                <el-option label="Duns & Ros" value="3"></el-option>
                                <el-option label="Govier, Aziz & Fogarasi" value="3"></el-option>
                                <el-option label="Gray (modified)" value="3"></el-option>
                                <el-option label="Gray (original)" value="3"></el-option>
                                <el-option label="Hagedorn & Brown" value="3"></el-option>
                                <el-option label="Hagedorn & Brown, Duns & Ros map" value="3"></el-option>
                                <el-option label="Mukherjee & Brill" value="3"></el-option>
                                <el-option label="No Slip Assumption" value="3"></el-option>
                                <el-option label="Orkiszewski" value="3"></el-option>
                            </el-select>
                             <el-popover
                                placement="top-start"
                                title="关联关系（Correlation）说明"
                                width="700"
                                trigger="click"
                                >
                                <el-row>
                                    Select the appropriate global, vertical, or horizontal correlation method. Available correlation methods depend on the source selected. 
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                             </el-popover>
                    </el-form-item>
                     <el-form-item label="摩擦率（Friction factor）:" label-width="250px" prop="">
                         <el-input placeholder="请填写摩擦率" style="width: 230px"></el-input>
                           <el-popover
                                placement="top-start"
                                title="摩擦率（Friction factor）说明"
                                width="700"
                                trigger="click"
                                >
                                <el-row style="">
                                    <p>One of two factors used to adjust the friction and holdup prediction of a particular flow correlation. The default value is 1. </p>

                                    <span style="font-weight:800">Note:</span> A linear relationship is used for the friction pressure drop. For example, if you set the friction factor to 0.5, the friction element of pressure drop computed by the correlation is halved. The two factors are used often as calibration factors when a good match to field data cannot be obtained by any other method. Changing these factors affects the results. Use with caution. 
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                            </el-popover>
                     </el-form-item>
                     <el-form-item label="悬持系数（Holdup factor）:" label-width="250px" prop="">
                         <el-input placeholder="请填写悬持系数" style="width: 230px"></el-input>
                           <el-popover
                                placement="top-start"
                                title="悬持系数（Holdup factor）说明"
                                width="700"
                                trigger="click"
                                >
                                <el-row style="">
                                    <p>One of two factors used to adjust the friction and holdup prediction of a particular flow correlation. The default value is 1. </p>
                                    <span style="font-weight:800">Note:</span> A non-linear relationship is used to calculate the liquid holdup from the value predicted by the correlation. The default value is 1. The two factors are used often as calibration factors when a good match to field data cannot be obtained by any other method. Changing these factors affects the results. Use with caution. 
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                            </el-popover>
                     </el-form-item>
                </el-collapse-item>
                <el-collapse-item title="水平多相源(Horizontal multiphase source)" name="2">
                    <el-form-item label="源（Source）:" label-width="250px" prop="">
                        <!-- <el-input v-model="newsimulationmodel.simulationnameSim"  style="width: 260px" placeholder="请填写仿真模拟方案名称"> -->
                            <el-select v-model="select" placeholder="请选择" style="width: 330px">
                                <el-option label="Baker Jardine" value="1"></el-option>
                                <el-option label="Neotec" value="2"></el-option>
                                <el-option label="OLGAS" value="3"></el-option>
                                <el-option label="TUFFP Unified" value="3"></el-option>
                                <el-option label="Tulsa (Legacy 1989)" value="3"></el-option>
                            </el-select>
                              <el-popover
                                placement="top-start"
                                title="源（Source）说明"
                                width="700"
                                trigger="click"
                                >
                                <el-row style="height:300px;overflow:auto;">
                                    Defines the global, vertical, or horizontal source of the correlation. 
                                    <h3>Baker Jardine</h3>
                                    Developed by Schlumberger (originally Baker Jardine) and tested extensively. This value is the default source selection. 
                                    <h3>Neotec</h3>
                                    The Neotec flow correlations were developed by a company called Neotec based in Calgary. Neotec was formed in 1972 by Gary Gregory and Khalid Aziz, professors at the University of Calgary who specialized in Multiphase Flow research. Neotec developed several software applications used in the oil and gas industry, including WELLFLO, PIPEFLO and FORGAS. In 2010, Neotec was acquired by SPT Group and became part of Schlumberger in 2012 when Schlumberger acquired SPT Group. 
                                    <h3>OLGAS</h3>
                                    Based in large part on data from the SINTEF multiphase flow laboratory near Trondheim, Norway. The OLGA-S mechanistic models are applicable for all inclination angles, pipe diameters and fluid properties. The 2-phase model considers gas-liquid flow. The 3-phase model considers gas-oil-water flow. 
                                    <h3>TUFFP Unified</h3>
                                    Developed by the Tulsa University Fluid Flow Projects (TUFFP) research consortium. The models are applicable for all inclination angles, pipe diameters and fluid properties. The 2-phase model considers gas-liquid flow. The 3-phase model considers gas-oil-water pipe flow. 
                                    <h3>LedaFlow PM</h3>
                                    The LedaFlow Point Model is the steady-state version of the transient model developed by SINTEF in collaboration with Total and ConocoPhillips and commercialized by Kongsberg. It is applicable for all inclination angles, pipe diameters and fluid properties. The 2-phase model considers gas-liquid flow. The 3-phase model considers gas-oil-water flow. 
                                    <h3>Tulsa (Legacy 1989)</h3>
                                    Developed by the University of Tulsa, USA, and last modified by Professor Jim Brill, February 1989. The code is usually of academic quality and may return errors. No modifications have been made to accommodate extreme conditions or ensure mathematical stability. These models are included only for the purpose of validating calculations against publications and other simulators using the same code. Not recommended for general use. 
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                              </el-popover>
                    </el-form-item>
                    <el-form-item label="关联关系（Correlation）:" label-width="250px" prop="">
                            <el-select v-model="select" placeholder="请选择" style="width: 330px">
                                <el-option label="Beggs & Brill Original" value="2"></el-option>
                                <el-option label="Beggs &u Brill, Taitel Dukler map" value="3"></el-option>
                                <el-option label="Beggs & Brill Revised" value="3"></el-option>
                                <el-option label="Beggs & Brill Revised, Taitel Dukler map" value="3"></el-option>
                                <el-option label="Baker Jardine Revised" value="3"></el-option>
                                <el-option label="Dukler, AGA & Flanigan" value="3"></el-option>
                                <el-option label="Dukler, AGA & Flanigan (Eaton Holdup)" value="3"></el-option>
                                <el-option label="No Slip Assumption" value="3"></el-option>
                                <el-option label="Oliemans" value="3"></el-option>
                                <el-option label="No Slip Assumption" value="3"></el-option>
                                <el-option label="Xiao" value="3"></el-option>
                            </el-select>
                             <el-popover
                                placement="top-start"
                                title="关联关系（Correlation）说明"
                                width="700"
                                trigger="click"
                                >
                                <el-row>
                                    Select the appropriate global, vertical, or horizontal correlation method. Available correlation methods depend on the source selected. 
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                             </el-popover>
                    </el-form-item>
                     <el-form-item label="摩擦率（Friction factor）:" label-width="250px" prop="">
                         <el-input placeholder="请填写摩擦率" style="width: 230px"></el-input>
                           <el-popover
                                placement="top-start"
                                title="摩擦率（Friction factor）说明"
                                width="700"
                                trigger="click"
                                >
                                <el-row style="">
                                    <p>One of two factors used to adjust the friction and holdup prediction of a particular flow correlation. The default value is 1. </p>

                                    <span style="font-weight:800">Note:</span> A linear relationship is used for the friction pressure drop. For example, if you set the friction factor to 0.5, the friction element of pressure drop computed by the correlation is halved. The two factors are used often as calibration factors when a good match to field data cannot be obtained by any other method. Changing these factors affects the results. Use with caution. 
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                            </el-popover>
                     </el-form-item>
                     <el-form-item label="悬持系数（Holdup factor）:" label-width="250px" prop="">
                         <el-input placeholder="请填写悬持系数" style="width: 230px"></el-input>
                           <el-popover
                                placement="top-start"
                                title="悬持系数（Holdup factor）说明"
                                width="700"
                                trigger="click"
                                >
                                <el-row style="">
                                    <p>One of two factors used to adjust the friction and holdup prediction of a particular flow correlation. The default value is 1. </p>
                                    <span style="font-weight:800">Note:</span> A non-linear relationship is used to calculate the liquid holdup from the value predicted by the correlation. The default value is 1. The two factors are used often as calibration factors when a good match to field data cannot be obtained by any other method. Changing these factors affects the results. Use with caution. 
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                            </el-popover>
                     </el-form-item>
                </el-collapse-item>
                 <el-collapse-item title="其他" name="3">
                    <el-form-item label="Swap angle:" label-width="250px" prop="">
                         <el-input placeholder="请填写Swap angle" style="width: 230px"></el-input>
                            <span style="padding-left:10px;padding-right:10px">rad</span>
                                <el-popover
                                    placement="top-start"
                                    title="Swap angle说明"
                                    width="700"
                                    trigger="click"
                                    >
                                    <el-row style="">
                                        <p>Angle at which vertical correlations are used instead of horizontal correlations. For angles less than or equal to 45°, horizontal correlations are used. For angles greater than 45°, vertical correlations are used. </p>
                                        <img src="content/element_icons/swapangle.png"/>
                                    </el-row>
                                    <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                                </el-popover>
                        </el-form-item>
                         <el-form-item label="Single phase correlation:" label-width="250px" prop="">
                                  <el-select v-model="select" placeholder="请选择" style="width: 330px">
                                <el-option label="Moody" value="3"></el-option>
                                <el-option label="AGA" value="2"></el-option>
                                <el-option label="Panhandle 'A'" value="3"></el-option>
                                <el-option label="Panhandle 'B'" value="3"></el-option>
                                <el-option label="Hazen - Williams" value="3"></el-option>
                                <el-option label="Weymouth" value="3"></el-option>
                                <el-option label="Cullender - Smith" value="3"></el-option>
                            </el-select>
                                <el-popover
                                    placement="top-start"
                                    title="correlation说明"
                                    width="700"
                                    trigger="click"
                                    >
                                    <el-row style="">
                                        <p>Based on the flow correlation selected, you may need to enter a drag factor, flow efficiency, or C factor (see SP factor). Single phase flow is assumed if the liquid volume fraction is less than .0001 or greater than .99. </p>
                                    </el-row>
                                    <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                                </el-popover>
                        </el-form-item>
                </el-collapse-item>
          </el-collapse>
       
    </el-form> 
</template>
<script>
export default {
      data(){
        return{
            rules: {
                simulationnameSim: [
                  { required: true, message: '请填写仿真模拟方案名称', trigger: 'blur' },
                  { min: 3, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                ],
            },
            flowcorrelationmodel:{
                source:{
                    value:"",
                    remark:""
                }
            },
            select:""
        }
    },
    props:{
        // newsimulationmodel:{
        //     type:Array
        // }
    }
}
</script>