/**
 * 右键菜单
 * Created by kid on 2017/7/31.
 */
import {Node, Point} from './PB_Shape';
import {ZeroNodeDevice} from "./PB_Device";
import {Device} from "./PB_Device";
import {StateManager} from "./PB_StateManager";
import {ArcManager} from "./PB_ArcManager";
import {DeviceManager} from "./PB_DeviceManager";

//////////////////////////////////////////////// /////////////////////////////////////////////////   interface   ////////
////--------------------------------------------右键菜单项---------------------------------------------------------////
/**
 * text: string;  菜单项名称
 * disabled: boolean;  是否可用
 * handler?: Function;  点击后的处理方法
 * menu?: Array<ContextMenuItem>;  子菜单
 * glyph?: string;  图标
 * shortcut?:string;  快捷键
 */
interface ContextMenuItem {
    text: string;
    disabled: boolean;
    handler?: Function;
    menu?: Array<ContextMenuItem>;
    glyph?: string;
    shortcut?: string;
}


export class ContextMenu {
    static st_popContextMenu: Function;
    static st_clostContextMenu: Function;
    /**
     * 点击节点右键菜单
     * @param node
     * @returns Array<ContextMenuItem>
     */
    static st_createNodeContextMenu(node: Node): Array<ContextMenuItem> {
        console.log("关联节点")
        //关联节点子菜单
        let linkMenu: Array<ContextMenuItem> = [];

        //添加外部节点选项
        if (node.svgLayer.parentDevice) {
            linkMenu.push({
                text: '外部节点',
                disabled: false,
                handler: function () {
                    (node.svgLayer.parentDevice as ZeroNodeDevice).setInnerNode4relate(node);
                    ContextMenu.st_clostContextMenu()
                }
            });

        }

        //添加站、场阀室选项
        node.svgLayer.deviceMap.forEach(device => {
            console.log(device)
            if (device.getNodeNum() == 0) {
                linkMenu.push({
                    text: '#' + device.getTitle(),
                    disabled: false,
                    handler: function () {
                        (device as ZeroNodeDevice).setOutNode4relate(node);
                        ContextMenu.st_clostContextMenu()
                    }
                });
            }
        });


        //创建并返回主菜单
        return [
            {
                text: '断开节点',
                disabled: node.devicePosMap.size == 1,
                handler: function () {
                    let deviceID = node.devicePosMap.keys().next().value;
                    let devicePos = node.devicePosMap.get(deviceID);
                    node.svgLayer.deviceMap.get(deviceID).replaceNodeWithNewOne(devicePos);
                    ContextMenu.st_clostContextMenu()
                }

            },
            {
                text: "标记关联",
                disabled: linkMenu.length == 0,
                menu: linkMenu,
            }];
    }

    /**
     * 点击元件右键菜单
     * @returns Array<ContextMenuItem>
     */
    static st_createDeviceContextMenu(): Array<ContextMenuItem> {
        let SM = StateManager.getInstance();
        return [
            {
                text: '复制',
                disabled: !(SM.deviceSelectedArray && SM.deviceSelectedArray.length),
                handler: function () {
                    SM.copyDevices();
                    ContextMenu.st_clostContextMenu()
                },
                glyph: 'f24d@FontAwesome',
                shortcut: 'Ctrl+C',
            },
            {
                text: '剪切',
                disabled: !(SM.deviceSelectedArray && SM.deviceSelectedArray.length),
                handler: function () {
                    SM.cutDevices();
                     ContextMenu.st_clostContextMenu()
                },
                glyph: 'f0c4@FontAwesome',
                shortcut: 'Ctrl+X',
            },
            {
                text: '删除',
                disabled: !(SM.deviceSelectedArray && SM.deviceSelectedArray.length),
                handler: function () {
                    SM.deleteDevices();
                    ContextMenu.st_clostContextMenu()
                },
                glyph: 'f014@FontAwesome',
                shortcut: 'Delete',
            },
            {
                text: '属性',
                disabled: !(SM.deviceSelectedArray && SM.deviceSelectedArray.length),
                handler: () => {
                    let device = SM.deviceSelectedArray[0];
                    Device.st_PopAttributeDlg(device);
                    ContextMenu.st_clostContextMenu()
                }
            }
        ];
    }

    /**
     * 点击画布右键菜单
     * @param coordinate
     * @returns Array<ContextMenuItem>
     */
    static st_createCanvasContextMenu(coordinate: Point): Array<ContextMenuItem> {

        let SM = StateManager.getInstance();
        let bDisable: boolean = true;
        if (SM.deviceCopyArgsArray && SM.deviceCopyArgsArray.length) {
            bDisable = false;
        }
        return [
            {
                text: '粘贴',
                disabled: bDisable,
                handler: function () {
                    SM.pasteDevices(coordinate);
                    ContextMenu.st_clostContextMenu()
                },
                glyph: 'f0ea@FontAwesome',
                shortcut: 'Ctrl+V',
            }
        ];
    }

    /**
     * 工况面板窗口右键弹出菜单
     * @param index 工况索引
     * @param record 工况记录集
     * @returns 工况右键菜单
     */
    static st_createArcViewContextMenu(index?: number, record?: any): Array<ContextMenuItem> {
       //alert(DeviceManager.getInstance().hasDevice())
        //console.log(record)
        let isDeleteDisabled: boolean = false;
        //index等于0（默认工况）或index不存在（没有点击在item上）时，“删除”不可用
        if (index)
            isDeleteDisabled = true;
        //alert(isDeleteDisabled)
        return [
            {
                text: '新建工况',
                disabled: !DeviceManager.getInstance().hasDevice()&&isDeleteDisabled,
                handler: function () {
                    alert("")
                    //弹出新建工况对话框，对话框上需要设定工况名称
                    ArcManager.st_PopNewArcDlg();
                    ContextMenu.st_clostContextMenu()
                }
            },
            {
                text: '删除工况',
                disabled: !isDeleteDisabled,
                handler: function () {
                    //从树控件中删除相应的控件
                    ArcManager.st_DelArcFromTree(record);
                    ContextMenu.st_clostContextMenu()
                }
            }
        ];
    }

}

