<template>
      <el-form ref="newsimulationform" :model="newsimulationmodel" :rules="rules" label-width="90px">
          <el-collapse v-model="activeNames" accordion>
                <el-collapse-item title="气象数据(Air data)" name="1">
                     <el-form-item label="温度（Temperature）:" label-width="350px" prop="">
                              <el-input  style="width: 260px" placeholder=""></el-input>
                               <span style="padding-left:10px;padding-right:10px">degC</span>
                              <el-popover
                                placement="top-start"
                                title="温度（Temperature）说明"
                                width="700"
                                trigger="click"
                                >
                                <el-row >
                                    <p>Air temperature. This value is used for heat transfer calculations for flowlines in a land environment. Because the ambient temperature is constant for the system being modeled in most cases, setting a global value allows you to conveniently model the effects across the system. This value may be easily modified to account for diurnal or seasonal variations in air temperature to analyze the effects on pipeline hydraulics and compressor performance.  </p>
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                              </el-popover>
                    </el-form-item>
                    <el-form-item label="风速（Wind speed）:" label-width="350px" prop="">
                         <el-input  style="width: 260px" placeholder="" ></el-input>
                             <span style="padding-left:10px;padding-right:10px">m/s</span>
                             <el-popover
                                placement="top-start"
                                title="风速（Wind speed）"
                                width="700"
                                trigger="click"
                                >
                                <el-row>
                                    <p>Velocity of air used for heat transfer calculations. The default value (8.43 ft/s or 2.57 m/s) equals 5 knots (which, in meteorological terms, corresponds to a light breeze). </p>
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                             </el-popover>
                    </el-form-item>
                     <el-form-item label="气压(Atmospheric pressure）:" label-width="350px" prop="">
                         <el-input  style="width: 260px" placeholder="" ></el-input>
                             <span style="padding-left:10px;padding-right:10px">kpa a</span>
                             <el-popover
                                placement="top-start"
                                title="气压(Atmospheric pressure）"
                                width="700"
                                trigger="click"
                                >
                                <el-row>
                                    <p>The atmospheric pressure specification is used when converting from gauge to absolute units. The default value is based on standard conditions. </p>
                                    <p>Changing the reference atmospheric pressure is most commonly performed when modeling low pressure gas systems at higher elevations, and pressure data is entered in gauge units. </p>
                                    <p><span style="font-weight:800">Note:</span> If you change the atmospheric pressure and your chosen display unit for pressure is in gauge units, all pressure entries will update to reflect this change. If your chosen display unit is in absolute units, all pressure entries will remain unchanged. </p>                               
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                             </el-popover>
                    </el-form-item>
                </el-collapse-item>
                <el-collapse-item title="土壤数据(Soil data）" name="2">
                        <el-form-item label="土壤类型(Soil type）:" label-width="350px" prop="">
                                <el-select v-model="select" placeholder="请选择" style="width: 330px">
                                    <el-option label="Clay Soil (moist)" value="2"></el-option>
                                    <el-option label="Clay Soil (dry)" value="3"></el-option>
                                    <el-option label="Clay Soil (wet)" value="2"></el-option>
                                    <el-option label="Clay Soil (frozen)" value="3"></el-option>
                                    <el-option label="Gravel" value="2"></el-option>
                                    <el-option label="Gravel (sandy)" value="3"></el-option>
                                    <el-option label="Limestone" value="2"></el-option>
                                    <el-option label="Sandstone" value="3"></el-option>
                                    <el-option label="Ice" value="2"></el-option>
                                    <el-option label="Ice (-40C)" value="3"></el-option>
                                    <el-option label="Snow (loose)" value="3"></el-option>
                                    <el-option label="Snow (hard packed)" value="3"></el-option>
                                    <el-option label="User defined" value="3"></el-option>
                                </el-select>
                                <el-popover                                
                                    placement="top-start"
                                    title="土壤类型(Soil type）说明"
                                    width="700"
                                    trigger="click"
                                    >
                                    <el-row style="">
                                    One of various soil types may be selected if the soil conductivity is not known. The default value, Moist Clay, is common in onshore fields. However, for offshore applications, the conductivity is generally higher and a type such as Deepwater Gulf of Mexico may be more appropriate. This data has been compiled from various sources by Neotec.
                                    </el-row>
                                    <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                                </el-popover>
                        </el-form-item>
                    <el-form-item label="导热系数（Soil conductivity）:" label-width="350px" prop="">
                         <el-input  style="width: 260px" placeholder="" ></el-input>
                         <span style="padding-left:10px;padding-right:10px"> W/(m.K)</span>
                        

                             <el-popover
                                placement="top-start"
                                title="导热系数（Soil conductivity）说明"
                                width="700"
                                trigger="click"
                                >
                                <el-row>
                                    Representative soil conductivity for each soil type you chose. The property may be specified by setting the Soil type to User defined.                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                             </el-popover>
                    </el-form-item>
           </el-collapse-item>
    </el-collapse>
    
    </el-form> 
</template>
<script>
export default {
      data(){
        return{
            rules: {
                simulationnameSim: [
                  { required: true, message: '请填写仿真模拟方案名称', trigger: 'blur' },
                  { min: 3, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                ],
            },
            flowcorrelationmodel:{
                source:{
                    value:"",
                    remark:""
                }
            },
            select:""
        }
    },
    props:{
        // newsimulationmodel:{
        //     type:Array
        // }
    }
}
</script>