<template>
      <el-form ref="newsimulationform" :model="newsimulationmodel" :rules="rules" label-width="90px">
          <el-collapse v-model="activeNames" accordion>
                <el-collapse-item title="侵蚀选项(Erosion options)" name="1">
                     <el-form-item label="侵蚀（Erosion model）:" label-width="350px" prop="">
                              <el-input  style="width: 260px" placeholder=""></el-input>
                              <el-popover
                                placement="top-start"
                                title="侵蚀模型（Erosion model）说明"
                                width="700"
                                trigger="click"
                                >
                                <el-row style="">
                                    <p>By default, API 14 E is selected with its default properties. The API 14 E is the only available option through the user interface. This model comes from the American Petroleum Institute, Recommended Practice 14 E, to predict solid free erosion only. </p>
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                              </el-popover>
                    </el-form-item>
                    <el-form-item label="侵蚀速度常数（Erosion velocity constant）:" label-width="350px" prop="">
                         <el-input  style="width: 260px" placeholder="" ></el-input>
                             <span style="padding-left:10px;padding-right:10px">(kg/m/s2)^1/2</span>
                             <el-popover
                                placement="top-start"
                                title="侵蚀速度常数（Erosion velocity constant）说明"
                                width="700"
                                trigger="click"
                                >
                                <el-row>
                                    <p>Also referred as C-factor and is applicable to API 14 E model. This constant depends on several factors like pipe material, fluid properties, etc. and can be user defined.</p> 
                                    <p>The default value of erosion velocity constant is: 122 kg 0.5m -0.5s-1(SI units); 100 lbm 0.5ft -0.5s-1 (field units) </p>
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                             </el-popover>
                    </el-form-item>
                </el-collapse-item>
          
          <el-collapse-item title="腐蚀选项(Corrosion options)" name="2">
                     <el-form-item label="腐蚀模型（Corrosion model）:" label-width="350px" prop="">
                            <el-select v-model="select" placeholder="请选择" style="width: 330px">
                                <el-option label="de Waard (1995)" value="3"></el-option>
                                <el-option label="None" value="2"></el-option>
                            </el-select>
                              <el-popover
                                placement="top-start"
                                title="腐蚀模型（Corrosion model）说明"
                                width="700"
                                trigger="click"
                                >
                                <el-row >
                                    <p>The only available option is the de Waard (1995) corrosion model that calculates corrosion rate caused by presence of CO 2 dissolved in water. </p>
                                    <p>The concentration of CO 2 and Water are obtained from the fluid properties definition (black oil, compositional or ScaleChem generated PVT files). If either CO 2 or Water is absent in the fluid, the resulting corrosion rate reported will be zero. </p>
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                              </el-popover>
                    </el-form-item>
                    <el-form-item label="腐蚀效率（Corrosion efficiency）:" label-width="350px" prop="">
                         <el-input  style="width: 260px" placeholder="" ></el-input>
                             <el-popover
                                placement="top-start"
                                title="腐蚀效率（Corrosion efficiency）说明"
                                width="700"
                                trigger="click"
                                >
                                <el-row>
                                    <p>A multiplier, C c, to correct for inhibitor efficiency, or to match the field data. </p>
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                             </el-popover>
                    </el-form-item>
                    <el-form-item label="PH值（Actual pH）:" label-width="350px" prop="">
                              <el-radio-group >
                                <el-radio :label="Specify">Specify</el-radio>
                                <el-radio :label="Calculated">Calculated</el-radio>
                            </el-radio-group>
                             <el-popover
                                placement="top-start"
                                title="PH值（Actual pH）说明"
                                width="700"
                                trigger="click"
                                >
                                <el-row>
                                    <p>Options are to specify or calculate. When calculated, PIPESIM will calculate the pH as a function of CO 2 fugacity and temperature. If the pH is known, it may be specified. However, this is recommended only for analysis over a narrow range of pressures and temperatures.</p>
                                </el-row>
                                <el-button  icon="el-icon-view" circle slot="reference"></el-button>
                             </el-popover>
                    </el-form-item>
           </el-collapse-item>
    </el-collapse>
    
    </el-form> 
</template>
<script>
export default {
      data(){
        return{
            rules: {
                simulationnameSim: [
                  { required: true, message: '请填写仿真模拟方案名称', trigger: 'blur' },
                  { min: 3, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                ],
            },
            flowcorrelationmodel:{
                source:{
                    value:"",
                    remark:""
                }
            },
            select:""
        }
    },
    props:{
        // newsimulationmodel:{
        //     type:Array
        // }
    }
}
</script>