<template>
  <el-dialog title="新建工况" :visible.sync="dialogVisible" width="20%">
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      size="mini"
      class="demo-ruleForm"
    >
      <el-form-item label="工况名称：" prop="gkname">
        <el-input v-model="ruleForm.gkname" placeholder="请输入工况名称"></el-input>
      </el-form-item>
      <el-form-item label="工况描述：" prop="gkms">
        <el-input v-model="ruleForm.gkms" placeholder="请输入工况描述"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm');">提交</el-button>
        <el-button @click="dialogVisible = false;">取 消</el-button>
      </el-form-item>
    </el-form>
    <!-- <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
    </span>-->                                       
  </el-dialog>
</template>
<script>
export default {
  data() {
    return {
      ruleForm: {
        gkname: "",
        gkms: ""
      },
      rules: {
        gkname: [
          { required: true, message: "请输入工况名称！", trigger: "blur" },
          { min: 2, max: 12, message: "长度在 2 到 12 个字符", trigger: "blur" }
        ]
      },
      dialogVisible: false,
    };
  },
  props: {
    //接受Project全局变量
    project: null
  },
  methods: {
    opengkdialog() {
      this.dialogVisible = true;
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (!this.project.isArcExist(this.ruleForm.gkname)) {
          let projectArc=   this.project.newArc(this.ruleForm.gkname, this.ruleForm.gkms);
          this.$emit("treeAddArc", projectArc);
          this.dialogVisible = false;
          } else {
            this.$message.error("错了哦，已经存在该工况名称，请重新输入!");
          }
        }
      });
    }
  }
};
</script>


